import React, { useEffect, useState } from 'react'
import Header from './../pages/Header'
import ReactDOM from 'react-dom/client'
import { Button } from '@mui/material'
import './../assets/css/allPages.css'
import { Principal } from './Principal'
import Cadastro from './Cadastro'
import Inscript from './Inscript'
import { Link } from 'react-router-dom'


const RegisterCenter = ({ user, isLoggedIn }) => {

    return (
        <div className='home'>
            <Header auth={isLoggedIn} user={user} />
            <div>
                <Button
                    color="primary"
                    variant="contained"
                    style={{ borderRadius: '0px 5px 5px 0px', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                    onClick={() => {
                        //ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Principal /></React.StrictMode>)
                        window.location.href = '/'
                    }}
                > Principal
                </Button>

                <Button
                    color="primary"
                    variant="contained"
                    style={{ boxShadow: 'none', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                    onClick={() => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Cadastro /></React.StrictMode>)
                    }}
                > Cadastro
                </Button>

                <Button
                    color="primary"
                    variant="contained"
                    style={{ boxShadow: 'none', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                    onClick={(e) => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Inscript /></React.StrictMode>)
                    }}
                > Realizar inscrio de Estudante
                </Button>
                <Link to='/repositorio'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ boxShadow: 'none', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                        onClick={(e) => {
                            //ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Inscript /></React.StrictMode>)
                        }}
                    > Histrico registos
                    </Button>
                </Link>
            </div>
            <hr></hr>
            <div id='desktop'>
                <Principal />
            </div>
        </div>
    )
}

export default RegisterCenter