import './../assets/css/moduloForms.css'
import { useForm, useStep } from "react-hooks-helper"
import { StepA } from "./modulSteps/stepA"
import { StepB } from "./modulSteps/stepB"
import { StepC } from "./modulSteps/stepC"
import { StepD } from "./modulSteps/stepD"
import { StepLast } from "./modulSteps/stepLast"
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import ReactDOM from 'react-dom/client'
import Cadastro from './Cadastro'
import React, { useEffect, useState } from 'react'
import { Button } from '@mui/material'

const modulo = {
    codigo: "",
    titulo: "",
    descri: "",
    nivel: "",
    area: "",
    sub_area: "",
    curso: "",
    creditos: "",
    data_de_registo: "",
    data_de_registo_revisao: "",
    elementos_de_competencia: [],
}

const defaultData = {
    elemento: "",
    numero_de_ra: ""
}


const steps = [
    { id: "stepA" },
    { id: "stepB" },
    { id: "stepC" },
    { id: "stepD" },
    { id: "stepLast" },
]

function Modulo() {
    const [formData, setForm] = useForm(modulo)
    const [elementData, setElement] = useForm(defaultData);


    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    })

    const props = { formData, setForm, navigation, elementData, setElement }

    const { go } = navigation

    switch (step.id) {

        case "stepA":
            return (
                <div className='grid-post'>
                    <span style={{ color: 'black' }}>Registar Unidade</span>
                    <hr></hr>
                    <StepA {...props} />
                    <hr style={{ marginBottom: '50px' }}></hr>
                </div>
            )

        case "stepB":
            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Registar Unidade</span>
                    <hr></hr>
                    <StepB {...props} />
                    <hr style={{ marginBottom: '50px' }}></hr>
                </div>
            )

        case "stepC":
            return (
                <div className='grid-post'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                        onClick={() => {
                            go('stepA')
                        }}
                    >
                        <ArrowBackIosIcon />
                    </Button> | <span style={{ color: 'black' }}>Registar Unidade</span>
                    <hr></hr>
                    <StepC {...props} />
                </div>
            )

        case "stepD":
            return (
                <div className='grid-post'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                        onClick={() => {
                            go('stepC')
                        }}
                    >
                        <ArrowBackIosIcon />
                    </Button>
                    | <span style={{ color: 'black' }}>Registar Unidade</span>
                    <hr></hr>
                    <StepD {...props} />
                </div>
            )

        case "stepLast":

            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Terminar registo</span>
                    <hr></hr>
                    <StepLast {...props} />
                </div>
            )

        default:
    }



}

export default Modulo