import { Navigate, Route, Routes } from "react-router-dom"
import Login from "./pages/Login"
import Home from "./pages/Home"
import Signup from "./pages/Signup"
import RegistarEvidencia from "./components/RegistarEvidencia"
import Dashboard from "./admin/Dashboard"
import axios from "axios"
import './assets/css/App.css'
import { useEffect, useState } from "react"
import Loader from "./pages/Loader"
import Loger from "./pages/Loger"
import RegisterCenter from "./register/RegisterCenter"
import { ErrorPage } from "./pages/ErrorPage"
import Historico from "./components/Historico"
import Menu from "./repositorio/Menu"

const App = () => {
  const [loading, setLoading] = useState(true)
  const [user, setUser] = useState({})
  const [isAdmin, setAdmin] = useState(false)
  const [isUser, setRegUser] = useState(false)
  const [isLoggedIn, setLoggedIn] = useState(false)

  useEffect(() => {
    axios.get('/api/user/me').then(res => {
      console.log(res);
      if (res.data.status === 'success') {
        if (res.data.data.data.role === 'formador') {
          setUser(res.data.data.data)
          setLoggedIn(true)
          setLoading(false)
        }
        if (res.data.data.data.role === 'admin') {
          setUser(res.data.data.data)
          setAdmin(true)
          setLoading(false)
        }
        if (res.data.data.data.role === 'user') {
          setUser(res.data.data.data)
          setRegUser(true)
          setLoading(false)
        }
      }
    }).catch(e => {
      const err = { e }
      setLoggedIn(false)
      setUser({ email: "", role: "", username: "" })
      setLoading(false)
    })
  }, [])


  return (
    <div className="container">
      <Routes>
        <Route path={'/'} element={loading ? <Loader /> : isAdmin ? <Navigate replace to="/dashboard" /> : isUser ? <Navigate replace to="/regcenter" /> : <Home user={user} isLoggedIn={isLoggedIn} />} />
        <Route path='/registopedagogico' element={loading ? <Loger /> : isLoggedIn ? <RegistarEvidencia user={user} isLoggedIn={isLoggedIn} /> : <Navigate replace to="/login" />} />
        <Route path='/historico' element={loading ? <Loger /> : isLoggedIn ? <Historico user={user} isLoggedIn={isLoggedIn} /> : <Navigate replace to="/login" />} />
        <Route path='/login' element={loading ? <Loger /> : isLoggedIn ? <Navigate replace to="/" /> : isAdmin ? <Navigate replace to="/dashboard" /> : <Login />} />
        <Route path='/signup' element={<Signup />} />

        {isAdmin ? <Route path='/dashboard' element={<Dashboard user={user} isLoggedIn={isAdmin} />} /> : <Route path='/dashboard' element={<Navigate replace to="/" />} />}

        {isUser ? <Route path='/regcenter' element={<RegisterCenter user={user} isLoggedIn={isUser} />} /> : <Route path='/regcenter' element={<Navigate replace to="/" />} />}

        <Route path='/repositorio' element={loading ? <Loger /> : isAdmin ? <Menu user={user} isLoggedIn={isAdmin} /> : isUser ? <Menu user={user} isLoggedIn={isUser} /> : <Navigate replace to="/" /> } />

        {/*Admin routes*/}
        <Route path='/logging' element={<Login />} />
        <Route path='*' element={<ErrorPage />} />
      </Routes>
    </div>
  )
}

export default App
