import img from './../assets/image/modules.jpg'
import imgAdm from './../assets/image/adm.png'
import imgPass from './../assets/image/password.jpg'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import CardMedia from '@mui/material/CardMedia'
import { Button } from '@material-ui/core'
import { useForm, useStep } from 'react-hooks-helper'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import axios from 'axios'
import { StepA } from './formadorSteps/StepA'
import { StepB } from './formadorSteps/StepB'
import { StepC } from './formadorSteps/StepC'
import { User } from './userSteps/users'
import { Passwords } from './Passwords'

const steps = [{ id: "base" }, { id: "stepA" }, { id: "stepB" }, { id: "stepC" }]


export const Usuario = () => {
    const [loading, setLoading] = useState(true)
    const [formadores, setFormador] = useState([])
    const [entidade, setEntidade] = useState({})

    const [formArray, setArry] = useForm({
        modulos: []
    })


    const [formDataB, setFormB] = useForm({
        curso: "",
        nivel: ""
    })

    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    })
    useEffect(() => {
        setLoading(true)
        axios.get('api/formador/get').then(res => {
            setFormador(res.data.data.data)
        }).catch(e => { console.log(e)})
        setLoading(false)
    }, [])

    const props = { loading, setLoading, formDataB, setFormB, formArray, setArry, formadores, entidade, setEntidade, navigation }

    switch (step.id) {
        case "base":
            return (
                <div className="users">
                    <h4>Gestor de usurios</h4>
                    <hr></hr>
                    <div className="division">
                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={img}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Atribuir mdulo ao Formador
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                    onClick={() => { navigation.next() }}
                                >
                                    <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>


                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={imgPass}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Alterar credenciais
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                    onClick={() => {
                                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Passwords/></React.StrictMode>)
                                    }}
                                >
                                    <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>

                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={imgAdm}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Criar Usurios
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                    onClick={() => {
                                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><User/></React.StrictMode>)
                                    }}
                                >
                                   <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>

                    </div>
                </div>
            )

        case "stepA":
            return (
                <div className="users">
                    <StepA {...props} />
                </div>
            )

        case "stepB":
            return (
                <div className="users">
                    <StepB {...props} />
                </div>
            )

        case "stepC":
            return (
                <div className="users">
                    <StepC {...props} />
                </div>
            )
    }
}
