import React, { useState, useEffect } from 'react'
import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import Menu from '@mui/material/Menu'
import MenuIcon from '@mui/icons-material/Menu'
import Container from '@mui/material/Container'
import Avatar from '@mui/material/Avatar'
import Butt from '@mui/material/Button'
import Tooltip from '@mui/material/Tooltip'
import MenuItem from '@mui/material/MenuItem'
import { Link, useNavigate } from 'react-router-dom'
import Slide from '@mui/material/Slide'
import Dialog from '@mui/material/Dialog'
import axios from 'axios'
import { Button, CircularProgress, DialogContent, TextField } from '@mui/material'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})

const Header = ({ auth, user }) => {
  const [anchorElNav, setAnchorElNav] = useState(null)
  const [anchorElUser, setAnchorElUser] = useState(null)
  const [not, setNot] = useState(false)
  const [weit, setWeit] = useState(true)
  const [im, setIm] = useState(false)
  const [open, setOpen] = useState(false)

  console.log(auth);

  useEffect(() => {
    if (auth) {
      setWeit(false)
    } else {
      setNot(true)
      setIm(true)
    }
  }, [])

  const handleClose = () => {
    setOpen(false)
  }

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget)
  }
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget)
  }

  const handleCloseNavMenu = () => {
    setAnchorElNav(null)
  }

  const handleCloseUserMenu = () => {
    setAnchorElUser(null)
  }

  const LogOut = () => {
    setAnchorElUser(null)
    axios.get(`/api/user/logout`)
      .then(res => {
        if (res.data.status === "success") {
          window.location.href = '/'
        }
      })
  }

  return (
    <AppBar position="static" className='he'>
      <Container maxWidth="x1">
        <Toolbar disableGutters>

          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
             {user.role === 'admin' ? <span>DASHBOARD <br/><span style={{fontSize: '10px', marginTop: '-10px'}}>Admin</span></span> : 
              user.role === 'user' ? <span>CENTRO DE REGISTO <br/><span style={{fontSize: '10px', marginTop: '-10px'}}>Pedaggico</span></span> : 
             'DOMS'}
          </Typography>

          {not || user.role === 'admin' ||  user.role === 'user' ? <span></span> :
            weit ? <span></span> :
              <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleOpenNavMenu}
                  color="inherit"
                >
                  <MenuIcon />
                </IconButton>

                <Menu
                  id="menu-appbar"
                  anchorEl={anchorElNav}
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                  }}
                  open={Boolean(anchorElNav)}
                  onClose={handleCloseNavMenu}
                  sx={{
                    display: { xs: 'block', md: 'none' },
                  }}
                >
                  <MenuItem onClick={handleCloseNavMenu}>
                    <Typography textAlign="center">
                      <Link to='/historico'>
                        Histrico de registo
                      </Link>
                    </Typography>
                  </MenuItem>

                </Menu>
              </Box>}

          {weit ? <span></span> : <img src={require(`./../assets/image/vex.png`)} alt="key" style={{ width: '50px', heigth: 'auto', margin: 'auto', display: 'block' }} />}
          {im ? <img src={require(`./../assets/image/vex.png`)} alt="key" style={{ width: '50px', heigth: 'auto', margin: 'auto', display: 'block' }} /> : <span></span>}
          <Typography
            variant="h5"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            {user.role === 'admin' ? 'DASHBOARD' : user.role === 'user' ? 'CNTR.REG': 'DOMS'}
          </Typography>


          {not || user.role === 'admin' ||  user.role === 'user' ? <span></span>
            :
            weit ? <span></span> :
              <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                <Link to='/registopedagogico'>
                  <Butt
                    variant='contained'
                    color="inherit"
                    style={{ marginLeft: '10px', textTransform: 'none' }}
                    onClick={handleCloseNavMenu}
                  >
                    Lanar evidncia
                  </Butt>
                </Link>

                <Link to='/historico'>
                  <Butt
                    variant='contained'
                    color="inherit"
                    style={{ marginLeft: '10px', textTransform: 'none' }}
                  >
                    Histrico de registo
                  </Butt>
                </Link>

              </Box>}

          {not ? <span>
            <Link to='/login'>
              <Butt
                variant='contained'
                color='warning'
                style={{ marginLeft: '10px' }}

              >
                REGISTAR ou ENTRAR
              </Butt>
            </Link>
          </span> : weit ? <CircularProgress size={19} /> :
            <Box sx={{ flexGrow: 0 }}>
              {user.username === '' ? <CircularProgress size={19} /> :
                <span style={{ marginRight: '5px', fontSize: '13px', fontFamily: 'verdana !important' }}>{user.username}</span>
              }

              {user.username === '' ? <CircularProgress size={19} /> :
                <Tooltip title="Open settings">
                  <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                    <Avatar alt={user.username} />
                  </IconButton>
                </Tooltip>
              }

              <Menu
                sx={{ mt: '45px' }}
                id="menu-appbar"
                anchorEl={anchorElUser}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorElUser)}
                onClose={handleCloseUserMenu}
              >


                <MenuItem onClick={() => {
                  setOpen(true)
                  handleCloseUserMenu()
                }}>
                  <Typography textAlign="center">Perfil</Typography>
                </MenuItem>

                <MenuItem onClick={handleCloseUserMenu}>
                  <Typography textAlign="center">Suporte</Typography>
                </MenuItem>

                <MenuItem onClick={LogOut}>
                  <Typography textAlign="center">Terminar sesso</Typography>
                </MenuItem>


              </Menu>
            </Box>}

        </Toolbar>
      </Container>

      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <p style={{ backgroundColor: 'skyblue', color: 'skyblue' }}>x</p>
        <DialogContent>
          <Tooltip title="Perfil">
            <IconButton sx={{ p: 1 }}>
              <Avatar alt={'Perfil photo'} /> <strong style={{ fontSize: '13px', marginLeft: '10px' }}>{user.username}</strong>
            </IconButton>
          </Tooltip>
          <hr></hr>
          <TextField
            label={"Usurio: " + user.email}
            size='small'
            disabled={true}
            style={{ marginBottom: '10px' }}
            fullWidth
          />
          <TextField
            label={user.username}
            size='small'
            style={{ marginBottom: '10px' }}
            fullWidth
          />
          <Button
            color="primary"
            variant="contained"
            style={{ marginTop: '10px', boxShadow: 'none', textTransform: 'none' }}
          > Salvar alterao</Button>
        </DialogContent>
      </Dialog>
    </AppBar>

  )
}

export default Header
