import React, { useEffect, useState } from 'react'
import Typography from '@mui/material/Typography'
import { Link } from 'react-router-dom'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import CardMedia from '@mui/material/CardMedia'
import { Button, CardActionArea, CircularProgress } from '@mui/material'
import Grid from '@mui/material/Grid'
import Header from './Header'
import Rodape from './Rodape'
import './../assets/css/allPages.css'
import img from './../assets/image/scanner.png'
import Skeleton from '@mui/material/Skeleton'


const Home = ({ user, isLoggedIn }) => {
    const [show, setSow] = useState(false)
    useEffect(() => {
        setTimeout(() => {
            setSow(true)
        }, 1000);
    }, [])

    return (
        <div className='home'>
            <Header auth={isLoggedIn} user={user}/>
            {isLoggedIn ? <Grid container spacing={0.5} style={{ marginTop: '20px', marginLeft: '3px' }}>
                <Grid item xs>
                    <Card sx={{ maxWidth: 345, marginTop: '20px' }}>
                        <Link to="/registopedagogico" style={{ textDecoration: 'none' }}>
                            <CardActionArea>
                                <CardMedia
                                    component="img"
                                    height="140"
                                    image={img}
                                    alt="green iguana"
                                />
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="div">
                                        Submeter <span style={{ color: 'silver' }}></span> <span style={{fontSize: '12px'}}>clique aqui</span>
                                    </Typography>
                                    <Typography variant="body2" color="text.secondary">
                                        <strong>Caro formador!</strong> <br />
                                        Submete a partir daqui as evidncias de Resultado de Aprendizagem
                                        que conferem as Unidades de Competncias  realizadas pelos formandos.
                                    </Typography>
                                </CardContent>
                            </CardActionArea>
                        </Link>
                    </Card>
                </Grid>
            </Grid> :
                show ?
                    <Grid container spacing={0.5} style={{ marginTop: '20px', marginLeft: '3px' }}>
                        <Grid item xs>
                            <Card sx={{ maxWidth: 345, marginTop: '20px' }}>
                                <CardActionArea>
                                    <CardMedia
                                        component="img"
                                        height="140"
                                        image={img}
                                        alt="green iguana"
                                    />
                                    <CardContent>
                                        <Typography gutterBottom component="div" style={{ textAlign: 'center' }}>
                                            <strong>REPOSITRIO EM NUVEM</strong>
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary" style={{ textAlign: 'center' }}>
                                            Plataforma de registo e arquivo lectrnico de evidncias de resultados de aprendizagem sobre o sistema de ensino baseado em competncia
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                            </Card>
                        </Grid>
                    </Grid>
                    :
                    <div>
                        <Grid container spacing={0} style={{ marginTop: '40px' }}>
                            <Grid item xs>
                                <Skeleton variant="rectangular" height={200} width={300} />
                            </Grid>
                        </Grid>
                    </div>
            }

            <Rodape />
        </div>
    )
}

export default Home