import { styled } from '@mui/material/styles'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemText from '@mui/material/ListItemText'
import Grid from '@mui/material/Grid'
import FolderIcon from '@mui/icons-material/Folder'
import RecentActorsIcon from '@mui/icons-material/RecentActors'
import Box from '@mui/material/Box'
import LinearProgress from '@mui/material/LinearProgress'
import { Button } from "@material-ui/core"
import { useEffect, useRef, useState } from 'react'
import DoneAllIcon from '@mui/icons-material/DoneAll'
import React from 'react'
import { Usuario } from '../Usuarios'
import ReactDOM from 'react-dom/client'

import axios from 'axios'

const Demo = styled('div')(({ theme }) => ({
    backgroundColor: theme.palette.background.paper,
}))

export const StepC = ({ entidade, formArray, navigation }) => {
    const { go } = navigation
    const [ms, setMs] = useState('')
    const [load, setLoader] = useState(false)
    const [act, setAct] = useState(false)
    const [progress, setProgress] = useState(0)
    const [buffer, setBuffer] = useState(10)
    const progressRef = useRef(() => { })

    useEffect(() => {
        progressRef.current = () => {
            if (progress > 100) {
                setLoader(false)
                
                if(act){
                    setMs('Registo efectuado com sucesso')
                    setTimeout(() => {
                        window.location.href = '/'
                    }, 500)
                }
                setProgress(0);
                setBuffer(10);
            } else {
                const diff = Math.random() * 10;
                const diff2 = Math.random() * 10;
                setProgress(progress + diff);
                setBuffer(progress + diff + diff2);
            }
        }
    })

    useEffect(() => {
        const timer = setInterval(() => {
            progressRef.current();
        }, 500)

        return () => {
            clearInterval(timer);
        }
    }, [])

    return (
        <div className="stepC">
            <span> <RecentActorsIcon fontSize="large" /> <strong>{entidade.nome_completo}</strong> | BI N: {entidade.bi} | Telefone: {entidade.contacto}</span>
            <hr></hr>
            <Grid item xs={12} md={6}>
                <Demo>
                    <List style={{ boxShadow: 'rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px' }}>
                        {formArray.modulos.map((el) => (<ListItem key={el.codigo}>
                            <FolderIcon fontSize='large' />
                            <ListItemText
                                style={{ marginLeft: '10px' }}
                                primary={el.titulo}
                                secondary={el.codigo + " | " + el.curso + " | " + el.nivel}
                            />
                        </ListItem>))}
                    </List>
                </Demo>
            </Grid>
            <hr></hr>

            {load ? <Box sx={{ width: '100%' }}>
                <LinearProgress variant="buffer" value={progress} valueBuffer={buffer} />
            </Box> : ms === '' ? <></> : <span style={{ color: 'green', textAlign: 'center', padding: '5px', position: 'relative', top: '-10px', left: '40px', border: '1px solid silver', width: '100%' }}>
                <strong><DoneAllIcon />{ms}</strong></span>}

            <Button
                color="default"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '3px', float: 'left', marginTop: '10px', boxShadow: 'none' }}
                onClick={() => {
                    window.location.href = '/'
                }}
            >
                Cancelar registo
            </Button>
            <Button
                color="primary"
                variant="contained"
                disabled={act}
                style={{ textTransform: 'none', borderRadius: '3px', float: 'right', marginTop: '10px', boxShadow: 'none' }}
                onClick={() => {
                    const ids = []
                    const registo = {
                        formador: entidade.bi,
                        modulos: formArray.modulos,
                        data: new Date().toLocaleString()
                    }

                    for (let index = 0; index < formArray.modulos.length; index++) {
                        ids.push({ codigo: formArray.modulos[index].codigo, formador: entidade.bi })
                    }

                    axios.post('/api/formador/criarRegisto', registo).then(res => {
                        if (res.statusText === 'Created') {
                            axios.post('/api/modulo/postCode', ids).then(result => {
                                console.log(result)
                            }).catch(e => { })
                        }else{
                            setMs('')
                        }
                    }).catch(e => {
                        setMs('')
                        console.log(e)
                        alert('Erro no registo, tente novamente!')
                    })
                    setAct(true)
                    setLoader(true)

                }}
            >
                Submeter e terminar registo
            </Button>
        </div >
    )
}