import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import { Grid } from "@material-ui/core"
import Paper from '@mui/material/Paper'
import React, { useEffect, useState } from 'react'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import ReactDOM from 'react-dom/client'
import { Usuario } from './Usuarios'
import { Button } from '@material-ui/core'
import axios from 'axios'
import { Skeleton } from '@mui/material'

export const Passwords = () => {
    const [loading, setLoading] = useState(true)
    const [rows, setRows] = useState([])

    useEffect(() => {
        axios.get('/api/user/pendings').then(res => {
            console.log(res)
            setRows(res.data.data.data)
            setLoading(false)
        }).catch(e => {
            setRows([])
        })
    }, [])

    return (
        <div className="users">
            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                onClick={() => {
                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Usuario /></React.StrictMode>)
                }}
            >
                <ArrowBackIosIcon />
            </Button> <span>Pedidos de alterao de senha</span>
            <Button
                color="primary"
                variant="outlined"
                style={{ textTransform: 'none', borderRadius: '3px', float:'right',marginTop: '0px', boxShadow: 'none' }}
                onClick={() => {
                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Passwords /></React.StrictMode>)
                }}
            >
                Actualizar lista
            </Button>
            <hr></hr>
            {loading ? <Grid>
                <Skeleton style={{ height: '60px', marginTop: '0px' }} />
                <Skeleton style={{ height: '200px', marginTop: '-53px' }} />
            </Grid> :
                <TableContainer component={Paper} style={{ marginTop: '15px' }}>
                    <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                <TableCell> <strong>Entidade</strong> </TableCell>
                                <TableCell> <strong>Messagem</strong></TableCell>
                                <TableCell> <strong>Nova senha</strong> </TableCell>
                                <TableCell> <strong>Data do pedido</strong></TableCell>
                                <TableCell>

                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(el => (<TableRow
                                key={el._id}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {el.email}
                                </TableCell>
                                <TableCell>{el.message}</TableCell>
                                <TableCell style={{ color: 'coral' }}>{el.senha}</TableCell>
                                <TableCell>{el.data}</TableCell>
                                <TableCell>
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        style={{ textTransform: 'none', marginTop: '0px', boxShadow: 'none' }}
                                        onClick={() => {
                                            const reset = {
                                                password: el.senha,
                                                passwordConfirm: el.senha,
                                                email: el.email
                                            }
                                            console.log(reset)
                                            axios.patch(el.url, reset).then(result => {
                                                console.log(result)
                                                if(result.data.status === "success"){
                                                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Passwords/></React.StrictMode>)
                                                }
                                            }).catch(e => {
                                                alert('Token is invalid or has expired')
                                            })
                                        }}
                                    >Aprovar alterao</Button>
                                </TableCell>
                            </TableRow>))}
                        </TableBody>
                    </Table>
                </TableContainer>}
        </div>
    )
}