import React from "react"
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import { Autocomplete } from "@mui/material"



export const Contact = ({ formData, setForm, navigation }) => {
  const optionsLocal = ['Maputo', 'Gaza', 'Inhambane', 'Sofala', 'Manica', 'Tete', 'Quelimane', 'Nampula', 'Niassa', 'Cabo Delegado', 'Estrangeiro*']
  const optionsLocalB = [
    'Moambicano',
    'Sul Africano',
    'Malawiano',
    'Zimbabueano',
    'Zambiano',
    ' Nigeriano',
    'Angolano',
    'Cabo Veridiano',
    'De outros Pases*'
  ]
  const { telefone, email, address, nation, province } = formData

  return (
    <div className="thirdForm">
    <h4>Passo 3 de 3</h4> <p style={{ fontSize: '12px', marginTop: '-10px' }}>Dados pessoais</p>
      <hr></hr>
      <Autocomplete
        options={optionsLocalB}
        style={{ marginBottom: '10px' }}
        size="small"
        renderInput={(params) =>
          <TextField {...params} name="nation" value={nation} onSelect={setForm} label="Nacionalidade" variant="outlined" />}
      />

      <Autocomplete
        options={optionsLocal}
        style={{ marginBottom: '10px' }}
        size="small"
        renderInput={(params) =>
          <TextField {...params} name="province" value={province} onSelect={setForm} label="Provncia" variant="outlined" />}
      />

      <TextField
        label="Endereo (morada)"
        name="address"
        value={address}
        onChange={setForm}
        margin="normal"
        variant="outlined"
        autoComplete="off"
        style={{ marginTop: '0px' }}
        fullWidth
        size="small"
      />

      <TextField
        label="Email"
        name="email"
        value={email}
        onChange={setForm}
        margin="normal"
        variant="outlined"
        autoComplete="off"
        style={{ marginTop: '0px' }}
        fullWidth
        size="small"
      />

      <TextField
        label="Telefone (+258)"
        name="telefone"
        value={telefone}
        onChange={setForm}
        margin="normal"
        variant="outlined"
        autoComplete="off"
        style={{ marginTop: '0px' }}
        type='tel'
        pattern="[0-9]*"
        maxLength="10"
        size="small"
      />


      <div style={{ marginTop: "-10px" }}>
        <Button
          color="secondary"
          variant="contained"
          style={{ marginRight: "" }}
          onClick={() => navigation.previous()}
        >
          Anterior
        </Button>
        <Button
          color="primary"
          variant="contained"
          onClick={() => navigation.next()}
        >
          Salvar
        </Button>
      </div>
    </div>
  )
}
