import React from "react"
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"

export const StepB = ({ formData, setForm, navigation, elementData, setElement }) => {

  var {
    elemento,
    numero_de_ra,
  } = elementData

  const {
    elementos_de_competencia
  } = formData

  var resultado_de_aprendizagem = {
    resultado_de_aprendizagem: elementData.elemento,
    RA_numero: elementData.numero_de_ra,
  }

  return (
    <div className="stepB">
      <h4>Passo 2 de 3</h4> <p style={{ fontSize: '13px', marginTop: '-10px' }}>Elementos de competncia</p>
      <hr></hr>
      <TextField
        label="RA N"
        helperText="Numero do Resultado de Aprendizagem"
        name="numero_de_ra"
        value={numero_de_ra}
        onChange={setElement}
        margin="normal"
        variant="outlined"
        type='number'
        autoComplete="off"
        style={{ width: 120, marginTop: "10px" }}
        size="small"
      />


      <TextField
        label="Elemento de Competncia/ Resultado de Aprendizagem"
        name="elemento"
        value={elemento}
        onChange={setElement}
        margin="normal"
        variant="outlined"
        autoComplete="off"
        style={{ marginTop: "5px" }}
        fullWidth
        size="small"
      />

      <Button
        color="secondary"
        variant="contained"
        style={{ marginTop: '10px', marginRight: '15px', boxShadow: 'none' }}
        onClick={() => {
          navigation.previous()
        }
        }
      >
        Anterior
      </Button>

      <Button
        color="primary"
        variant="contained"
        style={{ marginTop: '10px', marginRight: '5px' }}
        onClick={() => {
          if (elementos_de_competencia.length == 0) {
            if (elemento === '' || numero_de_ra === '') {
              alert('Campo(s) vazio(s) :: Por favor, prencher campo(s).')
            } else {
              elementos_de_competencia.push(resultado_de_aprendizagem)
              navigation.next()
            }
          } else {
            const result = elementos_de_competencia.find(({ RA_numero }) => RA_numero === resultado_de_aprendizagem.RA_numero)
            if (result === undefined || result === null) {
              elementos_de_competencia.push(resultado_de_aprendizagem)
              navigation.next()
            } else {
              console.log('has object')
              alert('Este nmero de Elemento de Competncia/Resultado de Aprendizagem j foi adicionado a unidade')
            }
          }
        }
        }
      >
        Adicionar este elemento
      </Button>
      <span style={{ margin: '5px' }}><strong>Ou/</strong></span>
      <Button
        color="primary"
        variant="contained"
        style={{ marginTop: '10px', boxShadow: 'none' }}
        onClick={() => {
          navigation.next()
        }}
      >Prosseguir para ver</Button>
    </div >
  )


}



