import React, { useState, useEffect } from 'react'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import axios from 'axios'
import img from './../assets/image/modules.jpg'
import cover from './../assets/image/capa.jpg'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import Typography from '@mui/material/Typography'
import CardMedia from '@mui/material/CardMedia'
import { Button, CardActionArea, CircularProgress } from '@mui/material'
import { useStep } from "react-hooks-helper"
import HistoricoNext from './stepCloud/HistorigoNext'
import Header from '../pages/Header'
import Report from './stepCloud/Report'
import BookmarksIcon from '@mui/icons-material/Bookmarks'
import Rodape from './../pages/Rodape'


const steps = [
    { id: "PassoA" },
    { id: "PassoB" },
    { id: "PassoC" },
]


const Historico = ({ user, isLoggedIn }) => {
    const [moduls, setModuls] = useState({})
    const [unidade, setUnidade] = useState({})
    const [entidade, setEntidade] = useState({})
    const [show, setShow] = useState(true)

    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    })

    useEffect(() => {
        axios.get(`/api/formador/mymoduls?email=${user.email}`)
            .then(res => {
                if (res.data.data.data.length == 0) {
                    setShow(false)
                    setModuls([])
                } else {
                    var mymdls = res.data.data.data
                    var ggt = []
                    for (let index = 0; index < res.data.data.data.length; index++) {
                        for (let i = 0; i < mymdls[index].modulos.length; i++) {
                            ggt.push(mymdls[index].modulos[i])
                        }
                    }
                    setModuls(ggt)
                    setShow(false)
                }
            }).catch(err => {
                console.log(err);
                setShow(false)
                setModuls([])
            })
    }, [])

    const props = { user, moduls, unidade, entidade, setEntidade, navigation }

    switch (step.id) {
        case "PassoA":
            return (
                <div className='hist'>
                    <Header auth={isLoggedIn} user={user} />
                    {show ? <div style={{ textAlign: 'center' }}>
                        <div style={{ marginLeft: 'auto', marginRight: 'auto', width: '50px', marginTop: '100px' }}>
                            <CircularProgress />
                        </div> <span style={{ color: 'green' }}>Processado Unidades do Formador</span> <br></br>
                        <span style={{ color: 'green', fontSize: '12px' }}>Por favor aguarde ...</span>
                    </div>
                        :
                        <div style={{ backgroundImage: `url(${cover})`, padding: '5px' }}>
                            <h4><BookmarksIcon/>Meus mdulos</h4>
                            <hr></hr>
                            <div className='division'>
                                {moduls.map((el) => (
                                    <div key={el.codigo}>
                                        <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                            <CardActionArea>
                                                <CardMedia
                                                    component="img"
                                                    height="120"
                                                    image={img}
                                                    alt="mec"
                                                />
                                                <CardContent>
                                                    <Typography gutterBottom component="div">
                                                        <strong>{el.curso}</strong>
                                                    </Typography>
                                                    <strong style={{color: 'coral'}}>{el.codigo}</strong><br></br>
                                                    <em>{el.titulo}</em><br></br>
                                                     <span style={{ fontSize: '12px', fontWeight: 'bold' }}>{el.nivel}</span>
                                                </CardContent>
                                            </CardActionArea>
                                            <Button
                                                color="primary"
                                                variant="contained"
                                                fullWidth
                                                style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                                onClick={() => {setUnidade(el); navigation.next() }}
                                            >
                                                <ArrowForwardIcon />
                                            </Button>
                                        </Card>
                                    </div>))}
                            </div>
                        </div>}
                </div>
            )

        case "PassoB":
            return (
                <div className='desk'>
                    <Header auth={isLoggedIn} user={user} />
                    <HistoricoNext {...props} />
                </div>
            )

        case "PassoC":
            return (
                <div className='desk'>
                    <Header auth={isLoggedIn} user={user} />
                    <Report {...props} />
                </div>
            )

        default:
    }
}

export default Historico
