//import SwipeableViews from 'react-swipeable-views'
import { useTheme } from '@mui/material/styles'
import AppBar from '@mui/material/AppBar'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import React, { useEffect, useState } from "react"
import Typography from '@mui/material/Typography'
import PropTypes from 'prop-types'
import Box from '@mui/material/Box'
import { useForm } from 'react-hooks-helper'
import { TextField, Button, TableContainer, CircularProgress, Grid, LinearProgress } from '@material-ui/core'
import { Alert, Autocomplete, Skeleton } from '@mui/material'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import './../assets/css/rigth.css'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import Rodape from '../pages/Rodape'
import axios from 'axios'
import { Buffer } from 'buffer'
import ReactDOM from 'react-dom/client'


function TabPanel(props) {
    const { children, value, index, ...other } = props
    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`action-tabpanel-${index}`}
            aria-labelledby={`action-tab-${index}`}
            {...other}
        >
            {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
        </Typography>
    )
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
}

function a11yProps(index) {
    return {
        id: `action-tab-${index}`,
        'aria-controls': `action-tabpanel-${index}`,
    }
}



function Inscript() {
    const theme = useTheme()
    const [value, setValue] = useState(0)
    const [student, setStudent] = useState({})
    const [moduls, setModuls] = useState([])
    const [skeleton, setSkeleton] = useState(false)
    const [process, setProcess] = useState(false)
    const [active, setActive] = useState(false)
    const [year, setYear] = useState('')
    const [bufferFile, setBufferFile] = useState({})
    const [printer, setPrinter] = useState(false)
    const [spin, setSpin] = useState(false)
    const [success, setSuccess] = useState(false)
    const [validater, setValidater] = useState(false)
    const [formData, setForm] = useForm({ codigoEstudante: '', cv: '', ano: '' })

    const { codigoEstudante, cv, ano } = formData

    const handleChange = (event, newValue) => {
        setValue(newValue)
    }

    const handleChangeIndex = (index) => {
        setValue(index)
    }

    useEffect(() => {
        if(formData.ano === ''){
            setYear(new Date().getFullYear())
        }else{
            setYear(formData.ano)
        }
    }, [])
    

    return (
        <div className='grid-post'>
            <h4><ArrowForwardIcon /> Inscrio  {year}</h4>
            <Button
                variant='contained'
                color="primary"
                style={{ float: 'right', boxShadow: 'none', marginLeft: '5px',textTransform: 'none', borderRadius: '0px 5px 5px 0px', marginTop: '10px' }}
                onClick={() => setYear(formData.ano)}
            >
                Alterar ano de lanamento
            </Button>
            <select name="ano" value={ano} onChange={setForm} style={{ float: 'right', marginTop: '10px', marginRight: '5px', margin: '12px 0px 0px 10px' }}>
                <option value={`${new Date().getFullYear()}`}>{`${new Date().getFullYear()}`}</option>
                <option value={`${new Date().getFullYear() - 1}`}>{`${new Date().getFullYear() - 1}`}</option>
                <option value={`${new Date().getFullYear() - 2}`}>{`${new Date().getFullYear() - 2}`}</option>
                <option value={`${new Date().getFullYear() - 3}`}>{`${new Date().getFullYear() - 3}`}</option>
                <option value={`${new Date().getFullYear() - 4}`}>{`${new Date().getFullYear() - 4}`}</option>
                <option value={`${new Date().getFullYear() - 5}`}>{`${new Date().getFullYear() - 5}`}</option>
                <option value={`${new Date().getFullYear() - 5}`}>{`${new Date().getFullYear() - 6}`}</option>
            </select>
            <hr></hr>
            <div>
                <TextField
                    size="small"
                    name="codigoEstudante"
                    value={codigoEstudante}
                    onChange={setForm}
                    label="Cdigo de Estudante"
                    style={{ width: 250, marginTop: '10px' }}
                    variant="outlined"
                />
                <Autocomplete
                    options={['Certificado Vocacional III', 'Certificado Vocacional IV', 'Certificado Vocacional V']}
                    style={{ width: 250, marginTop: '15px' }}
                    size="small"
                    renderInput={(params) =>
                        <TextField name="cv" value={cv} onSelect={setForm} {...params} label="Nvel/Certificado Vocacional" variant="outlined" />}
                />
                <Button
                    color="primary"
                    variant="contained"
                    disabled={printer}
                    style={{ marginTop: '15px', textTransform: 'none', boxShadow: 'none' }}
                    onClick={() => {
                        console.log(formData)
                        if (formData.codigoEstudante === '' || formData.cv === '') {
                            alert('Prencher campo/Selecionar opo.')
                        } else {
                            setProcess(true)
                            setSkeleton(true)
                            axios.get(`/api/estudante/getOne?codigo=${formData.codigoEstudante}`).then(res => {
                                console.log(res);
                                if (res.data.data.data === null || res.data.data.data === undefined) {
                                    setStudent({})
                                    setModuls([])
                                    alert('Este estudante no existe.')
                                    setProcess(false)
                                    setSkeleton(false)
                                } else {

                                    setStudent(res.data.data.data)

                                    var ano = year
                                    var ref = formData.codigoEstudante + ano

                                    axios.get(`/api/estudante/validateInscript/${ref}`).then(result => {
                                        setValidater(result.data.exist)
                                        if (result.data.exist) {
                                            setModuls([])
                                            setActive(false)
                                            setSkeleton(false)
                                        } else {
                                            axios.get(`/api/modulo/getOne?curso=${res.data.data.data.curso}&nivel=${formData.cv}`).then(response => {
                                                setModuls(response.data.data.data)
                                                setSkeleton(false)
                                            })
                                            setActive(true)
                                        }
                                    })

                                    //url:validateInscript:: validateInscript/id
                                    //Verity here if this student is allread inscripted
                                    //before get the moduls
                                    //consultar modulos em atrazo deste estudante a partir do codigo unico de inscricao modular
                                    //quantas evidencias sao N/A, e se o numero de N/A for igual a 3 ele nao pode se insc
                                    //crie uma colecao para registo de reprovacoes 

                                    setProcess(false)
                                }
                            })
                        }
                    }}
                >
                    Inscrever
                </Button>

                {student.codigoEstudante === null || student.codigoEstudante === undefined ? <span></span> :
                    <div className='studentView'>
                        Estudante: <strong>{student.firstName + " " + student.lastName}</strong><br></br>
                        Cdigo: <strong>{student.codigoEstudante}</strong><br></br>
                        BI n: <strong>{student.bi}</strong><br></br>
                        Curso: <strong>{student.curso}</strong><br></br>
                        Gnero: <strong>{student.genero}</strong><br></br>
                    </div>}

                {validater ? <Alert severity="error" style={{ marginTop: '15px' }}><em>Este estudante j se encontra inscrito</em></Alert> : <span></span>}

                <Box
                    sx={{
                        bgcolor: 'background.paper',
                        position: 'relative',
                        minHeight: 200,
                    }}
                    style={{ width: '100%', marginTop: '20px' }}
                > {process ? <LinearProgress></LinearProgress> : <span></span>}
                    <AppBar position="static" color="default" style={{ boxShadow: 'none', borderTop: '1px solid silver' }}>
                        <Tabs
                            value={value}
                            onChange={handleChange}
                            indicatorColor="primary"
                            textColor="primary"
                            variant="fullWidth"
                            aria-label="action tabs example"
                        >
                            <Tab label="Mdulos" {...a11yProps(0)} style={{ borderRight: '1px solid silver' }} />
                            <Tab label="Mdulos em atraso " {...a11yProps(1)} />
                        </Tabs>
                    </AppBar>
                    <Box
                        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                        index={value}
                        onChangeIndex={handleChangeIndex}
                        style={{ overflow: 'hidden' }}
                    >
                        {skeleton ?
                            <Grid>
                                <Skeleton style={{ height: '60px', marginTop: '0px' }} />
                                <Skeleton style={{ height: '100px', marginTop: '-31px' }} />
                                <Skeleton style={{ height: '100px', marginTop: '-38px' }} />
                            </Grid> :
                            <TabPanelComponent value={value} index={0} dir={theme.direction} moduls={moduls} process={process} />
                        }
                        <TabPanelComponent value={value} index={1} dir={theme.direction} moduls={[]} process={process} /> {/*set here the moduls for repeat array useState variable*/}
                    </Box>
                </Box>
                <hr></hr>
                {success ? <Alert severity="success" style={{ marginTop: '15px' }}><em>Inscrio realizada com sucesso</em></Alert> : <span></span>}

                <Button
                    color="primary"
                    variant="contained"
                    disabled={!active}
                    style={{ textTransform: 'none', boxShadow: 'none', marginLeft: '0px', marginTop: '10px', borderRadius: '0 30px 30px 0px', marginBottom: '5px' }}
                    onClick={() => {

                        setSpin(true)
                        var ano = year

                        const poster = {
                            inscript_data: {
                                codigoEstudante: student.codigoEstudante,
                                referencia: student.codigoEstudante + ano,
                                cv: formData.cv,
                                curso: student.curso,
                                ano: ano,
                                data: new Date().toLocaleString(),
                                modulos: moduls
                            },
                            student_data: {
                                nome: student.firstName + " " + student.lastName,
                                codigoEstudante: student.codigoEstudante,
                                cv: formData.cv,
                                bi: student.bi,
                                curso: student.curso,
                                telefone: student.telefone,
                                data: new Date().toLocaleString()
                            }
                        }
                        console.log(poster);

                        if (moduls.length == 0) {
                            console.log("sem modulos")
                            alert('Sem mdulos')
                            setSpin(false)
                        } else {

                            axios.post('/api/estudante/inscrever', poster).then(res => {
                                console.log(res)
                                setBufferFile(res.data.data.report)
                                setSuccess(true)
                                setStudent({})
                                setModuls([])
                            })
                            setTimeout(() => {
                                setSuccess(false)
                                setSpin(false)
                                setActive(false)
                                setPrinter(true)
                            }, 3000)
                        }
                    }}
                >
                    Submeter registo e inscrever {spin ? <CircularProgress size={25} style={{ position: 'relative', left: '10px', marginLeft: '20px', color: 'white' }} /> : ''}
                </Button>

                {printer ? <Button
                    color="primary"
                    variant="contained"
                    style={{ textTransform: 'none', boxShadow: 'none', float: 'right', marginLeft: '0px', marginTop: '10px', borderRadius: '0 30px 30px 0px', marginBottom: '5px' }}
                    onClick={() => {
                        const fileBuffer = Buffer.from(bufferFile.data, 'base64')
                        const blob = new Blob([fileBuffer], { type: 'application/pdf' });
                        const exportUrl = URL.createObjectURL(blob);
                        window.open(exportUrl)
                    }}
                >Imprimir recibo inscrio<CircularProgress size={25} style={{ position: 'relative', left: '10px', marginLeft: '20px', color: 'white' }} />
                </Button> : <span></span>}
                <hr></hr>
                <Button
                    color="secondary"
                    variant="contained"
                    style={{ textTransform: 'none', boxShadow: 'none', marginLeft: '0px', marginTop: '0px', marginBottom: '5px' }}
                    onClick={() => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Inscript /></React.StrictMode>)
                    }}>Terminar e limpar</Button>
            </div>
            <Rodape />
        </div>
    )
}

export const TabPanelComponent = ({ value, index, dir, moduls }) => (
    <TabPanel value={value} index={index} dir={dir}>
        <div style={{ float: index == 0 ? 'left' : 'right', position: 'relative', borderRadius: '5px', top: '-15px', fontSize: '12px', padding: '3px', paddingRight: '10px', paddingLeft: '10px', backgroundColor: index == 0 ? 'green' : 'red', color: 'white' }}>{index == 0 ? `Mdulos a frequentar (${moduls.length})` : `Mdulos a repetir (${moduls.length})`}</div>
        <TableContainer component={Paper} style={{ marginTop: '0px' }}>
            <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table" style={{ borderTop: '1px solid silver' }}>
                <TableHead>
                    <TableRow>
                        <TableCell> <strong>UC cdigo</strong> </TableCell>
                        <TableCell> <strong>Ttulo</strong></TableCell>
                        <TableCell> <strong>rea/Campo</strong> </TableCell>
                        <TableCell> <strong>Subrea</strong></TableCell>
                        <TableCell> <strong>Curso</strong></TableCell>
                        <TableCell> <strong>CV/Nvel</strong></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {moduls.map(el => (<TableRow key={el.codigo}
                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                        <TableCell component="th" scope="row">
                            {el.codigo}
                        </TableCell>
                        <TableCell>{el.titulo}</TableCell>
                        <TableCell>{el.area}</TableCell>
                        <TableCell>{el.sub_area}</TableCell>
                        <TableCell>{el.curso}</TableCell>
                        <TableCell>{el.nivel}</TableCell>
                    </TableRow>))}
                </TableBody>
            </Table>
        </TableContainer>
    </TabPanel>
)


export default Inscript