import { InputLabel } from '@material-ui/core'
import React from 'react'
import './../assets/css/App.css'
import './../assets/css/cadastro.css'
import { Button } from '@material-ui/core'
import Estudante from './Estudante'
import Modulo from './Modulo'
import ReactDOM from 'react-dom/client'


function Cadastro() {

    return (
        <div className='grid-post'>
            <div className="cardB">
                <div className="division">
                    <div className="ilustre">
                        <img src={require(`./../assets/image/etd.jpg`)} className="card-img" alt="..." />
                    </div>
                    <div className="text">
                        <div className="card-body">
                            <h5 className="card-title">ESTUDANTE</h5>
                            <p className="card-text" style={{ lineHeight: '16px', paddingRight: '10px' }}>
                                <InputLabel htmlFor="input-with-icon-adornment" style={{ marginBottom: '10px' }}>
                                    MATRICULAR NO DMS
                                </InputLabel>
                                Realize a partir daqui o cadastro de Rstudante
                            </p>
                        </div>
                    </div>

                    <div className='dir'>
                        <Button
                            className="buttonCadastrar"
                            onClick={() => {
                                ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode> <Estudante/> </React.StrictMode>)
                            }}
                        >
                            Cadastrar estudante
                        </Button>
                    </div>

                </div>
            </div>


            <div className="cardB">
                <div className="division">
                    <div className="ilustre">
                        <img src={require(`./../assets/image/modules.jpg`)} className="card-img" alt="..." />
                    </div>
                    <div className="text">
                        <div className="card-body">
                            <h5 className="card-title">MDULO</h5>
                            <p className="card-text" style={{ lineHeight: '16px', paddingRight: '10px' }}>
                                <InputLabel htmlFor="input-with-icon-adornment" style={{ marginBottom: '10px' }}>
                                    REGISTAR NO DMS
                                </InputLabel>
                                Realize a partir daqui o registo das Unidades de Competncia
                            </p>

                        </div>
                    </div>
                    <div className='dir'>
                        <Button
                            className="buttonCadastrar"
                            onClick={() => {
                                ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Modulo/></React.StrictMode>)
                            }}
                        >
                            Registar mdulo
                        </Button>
                    </div>
                </div>
            </div>

        </div>

    )
}

export default Cadastro