
import React, { useState } from 'react'
import Button from '@mui/material/Button'
import ButtonGroup from '@mui/material/ButtonGroup'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { green, pink } from '@mui/material/colors';
import Alert from '@mui/material/Alert';
import Avatar from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import FolderIcon from '@mui/icons-material/Folder';
import PageviewIcon from '@mui/icons-material/Pageview';
import AssignmentIcon from '@mui/icons-material/Assignment';
import Checkbox from '@mui/material/Checkbox';
import DeleteIcon from '@mui/icons-material/Delete';
import { useForm, useStep } from "react-hooks-helper";
import axios from 'axios'
import './../../assets/css/stepCss.css'
import { Autocomplete, Skeleton } from '@mui/material';

/*
var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cookie", "");*/

const dop = { code: '' }
export const PassoB = ({ moduls, dataModulo, setDataModulo, navigation }) => {
    //const { firstName, lastName, dateBirth, genero, local_nascimento } = formData
    //const [code, setCode] = useState(null)

    const [formData, setForm] = useForm(dop);
    const [comboErr, setComboErr] = useState(false)
    const [comboEr, setComboEr] = useState(false)
    const [row, setRow] = useState({})
    const [wait, setWiat] = useState(false)

    const {
        code
    } = formData

    console.log(dataModulo);


    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };



    const getModulo = () => {

        if (code === '') {
            setWiat(true)
            console.log('null')
            setComboErr(true)
            console.log('not found');
            setRow({})
            setWiat(false)
        } else {
            setRow({})
            setComboErr(false)
            setWiat(true)
            console.log(dataModulo)

            setTimeout(() => {
                var found
                for (let index = 0; index < dataModulo.length; index++) {
                    if (dataModulo[index].codigo === code) {
                        console.log('md encontrado');
                        console.log(dataModulo[index].codigo)
                        found = 'yes'
                        setRow(dataModulo[index])
                        setComboErr(false)
                        setComboEr(true)
                    }
                }

                if (found !== 'yes') {
                    setComboEr(false)
                    alert('Este estudante no se encontra inscrito neste mdulo')
                }

                setWiat(false)
            }, 500);


            /*
            console.log(code.target.value)
            fetch(`/mod/${code.target.value}`, requestOptions)
                .then(response => response.json())
                .then(result => {
                    if (result.modulo.length === 0) {
                        console.log(result)
                        setComboErr(true)
                        setDataModulo({})
                    } else {
                        setComboErr(false)
                        console.log(result)
                        setDataModulo(result.modulo[0])
                    }
                })
                .catch(error => {
                    console.log('error', error)
                })*/
        }
    }


    var peur = []
    for (let index = 0; index < moduls.length; index++) {
        peur.push(moduls[index].codigo)
    }
    const options = peur
    return (
        <div className="passoB">

            <div style={{ marginTop: '20px', marginLeft: '10px' }}>
                <Avatar sx={{ bgcolor: green[500] }}>
                    <AssignmentIcon />
                </Avatar>
                Mdulo</div>

            <div className='passoBox'>
                {/*   <TextField id="standard-basic" label="Cdigo do Mdulo" name='code' value={code} variant="standard" style={{ width: '200px' }} color={comboErr ? 'error' : 'success'} onChange={setForm} />*/}

                <Autocomplete
                    options={options}
                    style={{ width: 198, float: 'left', marginRight: '10px' }}
                    size="small"
                    renderInput={(params) =>
                        <TextField id="standard-basic" name="code" value={code} variant="standard" style={{ width: '200px' }} color={comboErr ? 'error' : 'success'} onSelect={setForm} {...params} label="Cdigo do Mdulo" />}
                />
                <Button
                    variant='contained'
                    color="primary"
                    style={{ marginTop: '10px', marginLeft: '0px', boxShadow: 'none',float: 'rigth' }}
                    onClick={getModulo}
                >
                    Submeter
                </Button>


                <div id='show'>
                    <TableContainer component={Paper} style={{ marginTop: '15px' }}>
                        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell> <strong>Cdigo</strong> </TableCell>
                                    <TableCell> <strong>Modulo (Titulo)</strong></TableCell>
                                    <TableCell> <strong>rea (Curso)</strong></TableCell>
                                    <TableCell> <strong>Certificado Vocacional (VC)</strong></TableCell>
                                    <TableCell>
                                        <Button variant="outlined" startIcon={<DeleteIcon />} onClick={() => { setRow({}); setComboEr(false) }}>
                                            Remover
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>

                                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                    <TableCell>{row.codigo}</TableCell>
                                    <TableCell>{row.titulo}</TableCell>
                                    <TableCell>{row.area}</TableCell>
                                    <TableCell>{row.nivel}</TableCell>
                                    <TableCell>
                                        {row.codigo == undefined || row.length == 0 ? <span></span> : <Checkbox {...label} value={row.codigo} onChange={(e) => console.log(e.target.checked)} />}
                                    </TableCell>
                                </TableRow>

                            </TableBody>
                        </Table>
                        {wait ? <Skeleton style={{ height: '70px', marginTop: '-10px', marginLeft: 'auto', marginRight: 'auto', width: '98%' }} /> : <span></span>}
                        {comboErr ? <Alert severity="error" style={{ width: '100%' }}>Mdulo no encontrado</Alert> : <span></span>}
                    </TableContainer>

                </div>




                <Box
                    sx={{
                        display: 'flex',
                        marginTop: '10px',
                        flexDirection: 'column',
                        alignItems: 'center',
                        '& > *': {
                            m: 1,
                        },
                    }}
                >
                    <ButtonGroup variant="outlined" aria-label="outlined button group">
                        <Button color="secondary"  onClick={() => navigation.previous()}>Anterior</Button>
                        <Button onClick={() => {

                            const isEmpty = Object.keys(row).length === 0
                            if (isEmpty) {
                                alert('Null process')
                            } else {
                                navigation.next(); setDataModulo(row)
                            }
                        }}>Prximo</Button>
                    </ButtonGroup>
                </Box>
            </div>
            {comboEr ? <Alert severity="warning" style={{ width: '100%' }}>Caro Formador! <br></br> certifique este modulo marcando a caixinha de seleco a direita. </Alert> : <span></span>}
        </div>
    )
}
