import TocIcon from '@mui/icons-material/Toc'
import { Button } from '@mui/material'
import RecentActorsIcon from '@mui/icons-material/RecentActors'
import axios from 'axios'

import React, { useState, useEffect } from 'react'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { CircularProgress } from '@mui/material'
import { Buffer } from 'buffer'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';

const Report = ({ unidade, entidade, navigation }) => {
    const [rows, setRows] = useState([])
    const [process, setProcess] = useState(true)

    useEffect(() => {
        axios.get(`/api/evidencia/getEvidencias?estudante=${entidade.estudante}&modulo=${entidade.modulo}&ano=${entidade.ano}`).then(res => {
            if (res.data.data.data.length == 0) {
                setRows([])
                setProcess(false)
            } else {
                setRows(res.data.data.data)
                setProcess(false)
            }
        }).catch(e => { setProcess(false) })
    }, [])

    return (
        <div style={{ marginTop: '30px',marginBottom: '100px' }}>
            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                onClick={() => {
                    navigation.previous()
                }}
            >
                <ArrowBackIosIcon />
            </Button> | <TocIcon fontSize='large' /> {unidade.titulo} | <strong style={{ color: 'coral' }}>{unidade.codigo}</strong>
            <hr></hr>
            <RecentActorsIcon fontSize='large' /> Estudante: <strong style={{ color: 'green' }}>{entidade.estudante}</strong> | <strong>{entidade.nome}</strong>
            <p style={{
                float: 'right',
                right: '0px',
                backgroundColor: 'coral',
                fontSize: '13px',
                position: 'relative',
                top: '0px',
                borderRadius: '5px 0px 0px 5px',
                padding: '2px 10px 2px 5px'
            }}><strong>Arquivo  {entidade.ano}</strong></p>
            <hr></hr>
            {process ? <div style={{ textAlign: 'center' }}>
                <div style={{ marginLeft: 'auto', marginRight: 'auto', width: '50px', marginTop: '20px' }}>
                    <CircularProgress size={20} />
                </div> <span style={{ color: 'green' }}>A carregar lista de estudantes</span> <br></br>
                <span style={{ color: 'green', fontSize: '12px' }}>Por favor aguarde ...</span>
            </div>
                :
                <TableContainer component={Paper} style={{ marginTop: '0px' }}>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', boxShadow: 'none', borderRadius: '5px 0px 0px 0px', marginTop: '5px', marginRight: '5px' }}
                        onClick={() => {
                            axios.post('/api/evidencia/getRI', {
                                estudante: entidade,
                                modulo: unidade,
                                evidencia: rows
                            }).then(res => {
                                console.log(res)
                                
                                const fileBuffer = Buffer.from(res.data.data.data.data, 'base64')
                                const blob = new Blob([fileBuffer], {
                                    type: 'application/pdf'
                                })
                                const fileURL = URL.createObjectURL(blob)
                                window.open(fileURL)
                            }).catch(e => console.log({ e }))
                        }}
                    >
                        Imprimir resultado individual
                    </Button>
                    <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                <TableCell> <strong>Cv/Nvel</strong></TableCell>
                                <TableCell> <strong>Material</strong></TableCell>
                                <TableCell> <strong>RA/AV</strong></TableCell>
                                <TableCell> <strong>Elemento</strong></TableCell>
                                <TableCell><strong>Classificao</strong></TableCell>
                                <TableCell><strong>Tipo de evidncia</strong></TableCell>
                                <TableCell><strong>Data de lanamento</strong></TableCell>
                                <TableCell><strong>Data de realizacao</strong></TableCell>

                                <TableCell><strong style={{ color: 'coral' }}>*ficheiro (Pdf)</strong></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(el => (<TableRow
                                key={el._id}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row" >
                                    {el.nivel}
                                </TableCell>
                                <TableCell>{el.material}</TableCell>
                                <TableCell>{el.material === 'Avaliao' ? '*' : el.numero_do_material}</TableCell>
                                <TableCell>{el.resultado_de_aprendizagem}</TableCell>
                                <TableCell style={{ color: el.nota >= 80 ? 'green': 'red'}}>{el.nota}  {el.classificacao}</TableCell>
                                <TableCell>{el.tipo_de_evidencia}</TableCell>
                                <TableCell>{el.data_de_lancamento}</TableCell>
                                <TableCell>{el.data_de_realizacao}</TableCell>
                                <TableCell>
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        style={{ textTransform: 'none', float: 'right', marginTop: '0px' }}
                                        onClick={() => {
                                            const fileBuffer = Buffer.from(el.arquivo_pdf.Buffer_PDF.data, 'base64')
                                            const blob = new Blob([fileBuffer], {
                                                type: 'application/pdf'
                                            })
                                            const fileURL = URL.createObjectURL(blob)
                                            window.open(fileURL)
                                        }}
                                    >
                                        Visualizar <PictureAsPdfIcon style={{ marginLeft: '5px' }} />
                                    </Button>
                                </TableCell>
                            </TableRow>))}
                        </TableBody>
                    </Table>
                </TableContainer>}
        </div>
    )
}

export default Report