import React, { useEffect, useState } from 'react'
import { useForm, useStep } from "react-hooks-helper"
import { PassoA } from "./stepForm/PassoA"
import { PassoB } from "./stepForm/PassoB"
import { PassoC } from "./stepForm/PassoC"
import Header from './../pages/Header'
import { PassoD } from './stepForm/passoD'
import axios from 'axios'


const modulo = {
    codigo: "",
    titulo: "",
    descri: "",
    nivel: "",
    area: "",
    sub_area: "",
    data_de_registo: "",
    data_de_registo_revisao: "",
    elementos_de_competencia: [],
}

const defaultData = {
    codigoEstudante: "",
    codigoModulo: "",
    tipo_de_evidencia: "",
    resultado_de_aprendizagem: "",
    classificacao: "",
    nota: "",
    numero_do_material: "",
    material: "",
    data_de_realizacao: "",
    data_de_lancamento: "",
    ano: "",
    tutela: ""
}

const steps = [
    { id: "PassoA" },
    { id: "PassoB" },
    { id: "PassoC" },
    { id: "PassoD" },
];

function RegistarEvidencia({ user, isLoggedIn }) {

    const [formData, setForm] = useForm(defaultData)
    const [anoRegisto, setAno] = useState({})
    const [dataModulo, setDataModulo] = useState({})
    const [moduls, setModuls] = useState([])
    const [dataEstudante, setDataEstudante] = useState({})
    const [pdf, setPdf] = useState({})
    const [evidenciaData, setEvidenciaData] = useState({})

    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    });

    useEffect(() => {
        axios.get(`/api/formador/mymoduls?email=${user.email}`)
            .then(res => {
                if (res.data.data.data.length == 0) {
                    setModuls([])
                } else {
                    var mymdls = res.data.data.data
                    var ggt = []
                    for (let index = 0; index < res.data.data.data.length; index++) {
                        for (let i = 0; i < mymdls[index].modulos.length; i++) {
                            ggt.push(mymdls[index].modulos[i])
                        }
                    }
                    setModuls(ggt)
                }
            }).catch(err => {
                console.log(err);
            })
    }, [])

    const props = { user, moduls, setModuls, dataEstudante, setDataEstudante, dataModulo, setDataModulo, pdf, setPdf, formData, setForm, evidenciaData, setEvidenciaData, anoRegisto, setAno, navigation };

    switch (step.id) {
        case "PassoA":
            return (
                <div className='grid-post'>
                    <Header auth={isLoggedIn} user={user} />
                    <PassoA {...props} />
                </div>
            )
        case "PassoB":
            return (
                <div className='grid-post'>
                    <Header auth={isLoggedIn} user={user} />
                    <PassoB {...props} />
                </div>
            )
        case "PassoC":
            return (
                <div className='grid-post'>
                    <Header auth={isLoggedIn} user={user} />
                    <PassoC {...props} />
                </div>
            )
        case "PassoD":
            return (
                <div className='grid-post'>
                    <Header auth={isLoggedIn} user={user} />
                    <PassoD {...props} />
                </div>
            )

        default:
    }

}



export default RegistarEvidencia