import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import './../assets/css/form.css'
import { useForm, useStep } from "react-hooks-helper"
import { Names } from "./studentSteps/Names"
import { ID } from "./studentSteps/ID"
import { Contact } from "./studentSteps/Contact"
import { Review } from "./studentSteps/Review"
import { Submit } from "./studentSteps/Submit"
import ReactDOM from 'react-dom/client'
import Cadastro from './Cadastro'
import React, { useEffect, useState } from 'react'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import { Button } from '@mui/material'

const defaultData = {
    firstName: "",
    lastName: "",
    dateBirth: "",
    local_nascimento: "",
    genero: "",
    bi: "",
    codigoEstudante: "",
    dateEmiBI: "",
    local_emissao: "",
    curso: "",
    nation: "",
    province: "",
    address: "",
    telefone: "",
    email: "",
    ingresso: new Date().toDateString()
};

const steps = [
    { id: "names" },
    { id: "id" },
    { id: "contact" },
    { id: "review" },
    { id: "submit" },
];

function Estudante() {

    const [formData, setForm] = useForm(defaultData);
    console.log(formData);

    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    });

    const props = { formData, setForm, navigation };

    switch (step.id) {
        case "names":
            return (
                <div className='grid-post'>
                    <span style={{ color: 'black' }}>Cadastrar Estudante</span>
                    <hr></hr>
                    <Names {...props} />
                </div>
            )
        case "id":
            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Cadastrar Estudante</span>
                    <hr></hr>
                    <ID {...props} />;
                </div>
            )
        case "contact":
            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Cadastrar Estudante</span>
                    <hr></hr>
                    <Contact {...props} />
                </div>
            )
        case "review":
            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Cadastrar Estudante</span>
                    <hr></hr>
                    <Review {...props} />
                    <hr></hr>
                </div>
            )
        case "submit":
            return (
                <div className='grid-post'>
                    | <span style={{ color: 'black' }}>Cadastro /</span>  <span style={{ marginTop: '0px' }}><strong>Terminar em</strong> <ArrowForwardIcon /></span>
                    <hr></hr>
                    <Submit {...props} />
                </div>
            )
        default:
    }
}



export default Estudante