import React from "react"
import Button from '@material-ui/core/Button'
import Accordion from '@material-ui/core/Accordion'
import AccordionSummary from '@material-ui/core/AccordionSummary'
import AccordionDetail from '@material-ui/core/AccordionDetails'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

import ListItemText from '@material-ui/core/ListItemText'

import IconButton from '@material-ui/core/IconButton'
import EditIcon from '@material-ui/icons/Edit'

import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogContent from '@mui/material/DialogContent'
import DialogContentText from '@mui/material/DialogContentText'
import DialogTitle from '@mui/material/DialogTitle'

export const Review = ({ formData, navigation }) => {
  //console.log(formData)
  const { go } = navigation
  const {
    firstName,
    lastName,
    dateBirth,
    local_nascimento,
    genero,
    bi,
    codigoEstudante,
    dateEmiBI,
    local_emissao,
    curso,
    nation,
    province,
    address,
    telefone,
    email,
  } = formData

  const [open, setOpen] = React.useState(false)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div className="lastForm">
      
      <h4>Reviso</h4><span><span style={{color: 'green'}}></span> <em>Carro usurio, confirme os dados antes que prosseguir para submisso</em></span>
      <hr></hr>
      <RenderAccordion txt="Dados pessoais" summary="names" go={go} details={[
        { 'Nome': firstName },
        { 'Apelido': lastName },
        { 'Data de Nascimento': dateBirth },
        { 'Local de Nascimento': local_nascimento },
        { 'Gnero ': genero },
      ]} />
      <hr></hr>
      <RenderAccordion txt="Identificao" summary="id" go={go} details={[
        { 'BI N/Passaport': bi },
        { 'Data de emisso': dateEmiBI },
        { 'Local de emisso': local_emissao },
        { 'Codigo de Estudante': codigoEstudante },
        { 'Curso': curso },
      ]} />
      <hr></hr>
      <RenderAccordion txt="Outros" summary="contact" go={go} details={[
        { 'Nacionalidade': nation },
        { 'Provncia': province },
        { 'Endereo': address },
        { 'Telefone': telefone },
        { 'Email': email },
      ]} />
      <hr></hr>
      <Button
        color="primary"
        variant="contained"
        style={{ marginBottom: '20px' }}
        onClick={() => {
         navigation.next()
        }}
      >
        Submeter cadastro
      </Button>

      <div>
        <Dialog
          open={open}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle style={{ color: 'red' }}>{"Mensagem"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Verifique todos os campos esto correctamente preenchidos! <br></br>
              (e)::
            </DialogContentText>
          </DialogContent>
          <DialogActions>

            <Button color="secondary" variant="contained" onClick={handleClose}>OK</Button>
          </DialogActions>
        </Dialog>
      </div>

    </div>
  )
}

export const RenderAccordion = ({ txt, summary, details, go }) => (
 <div>
    <AccordionSummary
      expandIcon={<ExpandMoreIcon />}
    >{txt}</AccordionSummary>
    <AccordionDetail>
      <div>
        {details.map((data, index) => {
          const objKey = Object.keys(data)[0]
          const objValue = data[Object.keys(data)[0]]
          return <ListItemText key={index} > <strong> {objKey} </strong> : {objKey === 'BI N/Passaport' || objKey === 'Codigo de Estudante' ? <span style={{ color: 'green' }}>{objValue}</span> :
            <span style={{ color: 'dodgerblue' }}><strong>{objValue}</strong></span>} </ListItemText>
        })}
        <Button
          color="primary"
          variant="outlined"
          style={{textTransform: 'none', marginTop: '15px'}}
          component="span"
          onClick={() => go(`${summary.toLowerCase()}`)}
        ><EditIcon /> Editar</Button>
      </div>
    </AccordionDetail>

    </div>
)


