import { Button, Grid } from "@material-ui/core"
import { Autocomplete } from "@mui/material"
import { TextField } from "@material-ui/core"
import Checkbox from '@mui/material/Checkbox'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import CircularProgress from '@mui/material/CircularProgress'
import { useState } from 'react'
import { useEffect } from 'react'
import { Skeleton } from '@mui/material'
import RecentActorsIcon from '@mui/icons-material/RecentActors'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import { Alert } from "@mui/material"
import Tooltip from '@mui/material/Tooltip'
import axios from "axios"
import Rodape from "../../pages/Rodape"

var mds = []
var god = []

export const StepB = ({ entidade, formDataB, setFormB, formArray, navigation }) => {

    const [ms, setMs] = useState(false)
    const [marked, setMarked] = useState(false)
    const [process, setProcess] = useState(false)
    const [setter, setSetter] = useState([])
    const [done, setDone] = useState(false)
    const [not, setNot] = useState(false)
    const [sizer, setSizer] = useState('')
    const [data, setData] = useState([])
    const [codes, setCodes] = useState([])
    const [skeleto, setSkeleto] = useState(false)

    const { curso, nivel } = formDataB

    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

    var {
        modulos
    } = formArray

    useEffect(() => {

        console.log(codes)
        mds = []
        god = []
        setTimeout(() => {
            setMs(true)
        }, 2000)

        axios.get('/api/modulo//validateAll').then(res => {
            console.log(res)
            if (res.data.data.data == 0) {
                setCodes([])
            } else {
                var yuri = []
                for (let index = 0; index < res.data.data.data.length; index++) {
                    yuri.push(res.data.data.data[index].codigo)
                }
            }
            setCodes(yuri)
        }).catch(e => { })

    }, [])


    return (
        <div>
            <span> <RecentActorsIcon fontSize="large" /> <strong>{entidade.nome_completo}</strong> | BI N: {entidade.bi} | Telefone: {entidade.contacto}</span>
            <hr></hr>

            <Alert severity="info" style={{ marginTop: '5px' }}>Caro usurio, realize a partir daqui atribuio de Unidade(s) de Competncia ao Formador <em><strong>{entidade.nome_completo}</strong></em>.</Alert>

            <Autocomplete
                options={['Mecnica Auto', 'Electricidade', 'Construo Civil', 'Mecnica Industrial']}
                disabled={not}
                style={{ marginTop: '10px' }}
                size="small"
                renderInput={(params) =>
                    <TextField {...params} name="curso" value={curso} onSelect={setFormB} label="Curso" variant="outlined" />}
            />

            <Autocomplete
                options={['Certificado Vocacional III', 'Certificado Vocacional IV', 'Certificado Vocacional V']}
                disabled={not}
                style={{ marginTop: '10px' }}
                size="small"
                renderInput={(params) =>
                    <TextField {...params} name="nivel" value={nivel} onSelect={setFormB} label="Certificado Vocacional" variant="outlined" />}
            />

            <Button
                color="primary"
                variant="contained"
                disabled={done}
                style={{ textTransform: 'none', boxShadow: 'none', marginLeft: '0px', marginTop: '10px', float: 'left', marginBottom: '10px' }}
                onClick={() => {
                    console.log(formDataB);
                    setSkeleto(true)
                    if (formDataB.curso === '' || formDataB.nivel === '') {
                        setSkeleto(false)
                        alert('Selectione /curso e /nvel pertencentes a unidade que pretende atribuir')
                    } else {
                        axios.get(`/api/modulo/getOne?curso=${formDataB.curso}&nivel=${formDataB.nivel}`).then(res => {
                            if (!res.data.data.data.length == 0) {
                                var go = []
                                for (let i = 0; i < res.data.data.data.length; i++) {
                                    god.push(res.data.data.data[i])
                                    go.push(res.data.data.data[i])
                                }
                                setData(go)
                                setTimeout(() => {
                                    setSkeleto(false)
                                }, 500);
                            } else {
                                setNot(false)
                                god = []
                                go = []
                                setData([])
                                setTimeout(() => {
                                    setSkeleto(false)
                                }, 500);
                            }
                        })
                    }
                }}
            >
                Selecionar UC's
            </Button>  <div style={{ width: '20px', height: '20px', backgroundColor: 'red', marginLeft: '200px', marginTop: '15px' }}></div><span style={{ color: 'red', fontSize: '13px', marginLeft: '58px' }}><strong>Mdulo(s) ocupado(s)</strong></span><br></br>

            <div className='show'>
                <hr></hr>
                <div style={{ marginTop: '5px' }}>
                    {process ? <div style={{ float: 'right', position: 'relative', top: '-15px', fontSize: '12px', padding: '3px', paddingRight: '10px', paddingLeft: '10px', backgroundColor: 'coral' }}>Mdulos seleccionados</div> : ''}
                    <ul style={{ display: 'inline-flex', listStyle: 'none' }}>
                        {setter.map(el => (
                            <li key={el.codigo} style={{ border: '1px solid silver', padding: '5px', margin: '3px', borderRadius: '20px' }}><strong>{el.codigo}</strong>
                                <Tooltip title={el.titulo}>
                                    <Button
                                        color="default"
                                        variant="contained"
                                        style={{ textTransform: 'none', boxShadow: 'none', borderRadius: '15px', marginLeft: '10px' }}
                                    ><span style={{ fontSize: '8xp' }}>Titulo</span></Button>
                                </Tooltip>
                            </li>
                        ))}
                    </ul>
                    <hr style={{ marginTop: '-10px' }}></hr>
                </div>

                {skeleto ?
                    <Grid>
                        <Skeleton style={{ height: '60px', marginTop: '0px' }} />
                        <Skeleton style={{ height: '100px', marginTop: '-31px' }} />
                        <Skeleton style={{ height: '100px', marginTop: '-38px' }} />
                    </Grid>
                    : <TableContainer component={Paper} style={{ marginTop: '15px' }}>
                        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell><strong>Codigo</strong> </TableCell>
                                    <TableCell> <strong>Titulo</strong> </TableCell>
                                    <TableCell> <strong>Curso</strong></TableCell>
                                    <TableCell> <strong>Area</strong></TableCell>
                                    <TableCell> <strong>Sub area</strong></TableCell>
                                    <TableCell> <strong>CV</strong></TableCell>
                                    <TableCell> {marked ? <CircularProgress size={20} style={{ float: '' }}></CircularProgress> : ""}</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {data.map((el) => (
                                    <TableRow
                                        key={el.codigo}
                                        style={{
                                            backgroundColor: el.codigo === mds.find(cod => cod === el.codigo) ? 'coral' : 'white',
                                        }}
                                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                    >

                                        {codes === undefined ? <TableCell component="th" scope="row">
                                            {el.codigo}
                                        </TableCell> :
                                            <TableCell component="th" scope="row" style={{
                                                backgroundColor: codes.includes(el.codigo) ? 'red' : 'white'
                                            }}>
                                                {el.codigo}
                                            </TableCell>}

                                        <TableCell>{el.titulo}</TableCell>
                                        <TableCell>{el.curso}</TableCell>
                                        <TableCell>{el.area}</TableCell>
                                        <TableCell>{el.sub_area}</TableCell>
                                        <TableCell>{el.nivel}</TableCell>
                                        <TableCell>
                                            {el.codigo == undefined || data.length == 0 ? <span></span> :
                                                codes === undefined ?
                                                    <Checkbox {...label} disabled={el.codigo === mds.find(cod => cod === el.codigo) ? true : false} checked={el.codigo === mds.find(cod => cod === el.codigo) ? true : false} value={el.codigo} onChange={(e) => {
                                                        setNot(true)
                                                        if (e.target.checked) {
                                                            
                                                            if (!mds.includes(e.target.value)) {
                                                                mds.push(e.target.value);
                                                                console.log('here');
                                                            }
                                                            setMarked(true)
                                                            setTimeout(() => {
                                                                setMarked(false)
                                                            }, 500)

                                                        } else {
                                                            mds = mds.filter(function (item) {
                                                                return item !== e.target.value
                                                            })

                                                        }

                                                        setSizer(`${mds.length}`)
                                                    }} />
                                                    :
                                                    codes.includes(el.codigo) ? <></> :
                                                        <Checkbox {...label} disabled={el.codigo === mds.find(cod => cod === el.codigo) ? true : false} checked={el.codigo === mds.find(cod => cod === el.codigo) ? true : false} value={el.codigo} onChange={(e) => {
                                                            setNot(true)
                                                            if (e.target.checked) {
                                                                if (!mds.includes(e.target.value)) {
                                                                    mds.push(e.target.value);
                                                                    console.log('here');
                                                                }
                                                                setMarked(true)
                                                                setTimeout(() => {
                                                                    setMarked(false)
                                                                }, 500)

                                                            } else {
                                                                mds = mds.filter(function (item) {
                                                                    return item !== e.target.value
                                                                })

                                                            }

                                                            setSizer(`${mds.length}`)
                                                        }} />}
                                        </TableCell>
                                    </TableRow>
                                ))}

                            </TableBody>
                        </Table>
                        {data.length == 0 ? <em style={{ marginLeft: '15px', color: 'silver' }}>Selecinar modulos */</em> : <></>}
                    </TableContainer>}
            </div>

            <Button
                color="default"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '3px', marginTop: '10px', height: '37px', float: 'left', boxShadow: 'none' }}
                onClick={() => {
                    window.location.href = '/'
                }}
            >
                <ArrowBackIosIcon /> Voltar e cancelar registo
            </Button>

            <Button
                color="primary"
                disabled={done}
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '100px', marginLeft: '0px', float: 'right', marginTop: '10px', marginBottom: '5px' }}
                onClick={() => {
                    setMarked(true)
                    if (mds.length == 0) {
                        setMarked(false)
                        alert('Por favor, marque pelo menos uma UC para este formador!')

                    } else {
                        const ids = god.map(o => o.codigo)
                        const filtered = god.filter(({ codigo }, index) => !ids.includes(codigo, index + 1))
                        for (let index = 0; index < mds.length; index++) {
                            for (let k = 0; k < filtered.length; k++) {
                                if (mds[index] === filtered[k].codigo) {
                                    modulos.push(filtered[k])
                                }
                            }
                        }

                        setSetter(modulos)
                        setDone(true)
                        setMarked(false)
                        setData([])
                        setProcess(true)
                    }
                }}
            >
                Adicionar [+] e anexar mdulos marcados
            </Button>


            <Button
                color="primary"
                variant="contained"

                style={{ textTransform: 'none', boxShadow: 'none', marginLeft: '0px', marginTop: '10px', borderRadius: '0 30px 30px 0px', marginBottom: '5px' }}
                onClick={() => {

                    if (sizer === '' || modulos.length == 0) {
                        alert('Por favor, marque e atribua pelo menos uma UC para este formador!')
                    } else {

                        console.log(modulos)
                        navigation.next()
                    }
                }}
            >
                Avanar para terminar registo {process ? <CircularProgress size={25} style={{ position: 'relative', left: '10px', marginLeft: '20px', color: 'white' }} /> : ''}
            </Button>


            <hr></hr>
            <Rodape />
        </div>
    )

}

