import React, { useEffect, useState } from "react"
import Button from "@material-ui/core/Button"
import { Alert } from '@mui/material'
import axios from "axios"

export const StepLast = ({ formData, navigation }) => {
  const [log, setLog] =useState('')
  const [error, setError] =useState(false)
  const [suc, setSuc] =useState(false)
  const [load, setLoad] =useState(false)
  const { go } = navigation

  return (
    <div>
     <Button
        color="primary"
        variant="contained"
        style={{ marginTop: '-58px', textTransform: 'none', float: 'right', boxShadow: 'none' }}
        onClick={() => {
          setLoad(true)
          console.log(formData)
          axios.post('api/modulo/registarModulo', formData).then(res => {
            console.log(res)
            if(res.data.status === "success"){
              setError(false)
              setSuc(true)
              setTimeout(() => {
                window.location.href = '/'
              }, 500);
              setLoad(false)
            }

            if(res.data.status === "error"){
              setError(true)
              setLog(res.data.log.message)
              setLoad(false)
            }

            if(res.data.code == 11000){
              setError(true)
              setLog('Esta Unidade j foi regista. => verifique o: '+ Object.keys(res.data.keyValue)[0])
              setLoad(false)
            }

          }).catch(e => {
            setLog('Erro de cadastro. Por favor, contacte o administrador::')
            setLoad(false)
          })
        }}
      >
        SUBMETER E TERMINAR
      </Button>

      <div>
      <div>
        {error ? <Alert severity="error">{log.split('.,').join('\n ')} <br></br>
        <Button
        color="secondary"
        variant="contained"
        style={{ marginTop: '10px', textTransform: 'none'}}
        onClick={() => go('stepA')}
        >Voltar para editar</Button> </Alert> : ''}
        {suc ? <Alert>Unidade registada com sucesso </Alert>: ''}
      </div>
      </div>

    </div>
  )
}
