import React, { useEffect, useState } from 'react'
import Button from '@mui/material/Button'
import ButtonGroup from '@mui/material/ButtonGroup'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { green, pink } from '@mui/material/colors';
import Avatar from '@mui/material/Avatar';
import PageviewIcon from '@mui/icons-material/Pageview';
import DeleteIcon from '@mui/icons-material/Delete';
import Checkbox from '@mui/material/Checkbox';
import { Alert } from '@mui/material'
import './../../assets/css/stepCss.css'
import { Skeleton } from '@mui/material';
import axios from 'axios'
import { useForm } from 'react-hooks-helper'


export const PassoA = ({ user, moduls, dataEstudante, setDataEstudante, setDataModulo, formData, setForm, anoRegisto, setAno, navigation }) => {
    //const { firstName, lastName, dateBirth, genero, local_nascimento } = formDataB
    const [formDataB, setFormB] = useForm({ codigo: '', ano: '' });
    const [year, setYear] = useState('')
    const [comboErr, setComboErr] = useState(false)
    const [wait, setWiat] = useState(false)

    const { codigo, ano } = formDataB

    useEffect(() => {
        if (formDataB.codigo === '') {
            setDataEstudante({})
            setDataModulo({})
        }
    }, [])


    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

    /*
    useEffect(() => {
        fetch("/est/95500", requestOptions)
            .then(response => response.json())
            .then(result => {
                console.log(result.estudante[0]);
                setDataEstudante(result.estudante[0])
            })
            .catch(error => {
                console.log('error', error)
            })
    }, [])*/

    const getEstudante = () => {
        

        if (formDataB.codigo === '') {
            console.log('null')
            setWiat(false)
        } else {
            setDataEstudante({})
            setComboErr(false)
            setWiat(true)
            
            var y = formDataB.ano === '' ? new Date().getFullYear() : formDataB.ano
            setYear(y)
            axios.get(`/api/estudante/inscript?codigo=${formDataB.codigo}&ano=${y}`).then(result => {
                
                if (result.data.data.data.codigoEstudante === undefined || result.data.data.data.codigoEstudante === null) {
                    setWiat(false)
                    //setComboErr(true)
                    console.log(result)
                    setDataEstudante({})
                } else {
                    setComboErr(false)
                    if (result.data.data.unidades === null || result.data.data.unidades === undefined) {
                        setDataModulo([])
                    } else {
                        setDataModulo(result.data.data.unidades.modulos)
                        setAno({ano: result.data.data.unidades.ano}) 
                        alert("ANO DE LANAMENTO DE EVIDNCIA: "+result.data.data.unidades.ano)
                    }
                    // setDataModulo(result.estudante[0].modulos)
                    setDataEstudante(result.data.data.data)
                    setWiat(false)
                }
            }).catch(error => {
                    console.log('error', error)
                    setWiat(false)
                    setComboErr(true)
                    setDataEstudante({})
                    setDataModulo({})
                })

        }

    }


    return (
        <div className="passoA">

            <div style={{ marginTop: '20px', marginLeft: '10px' }}>
                <Avatar sx={{ bgcolor: pink[500] }}>
                    <PageviewIcon />
                </Avatar>
                Estudante
            </div>

            <p style={{backgroundColor: 'dodgerblue', color: 'white',width: '85px', padding: '3px', borderRadius: '5px'}}>
                {year === ''? <span><strong> {new Date().getFullYear()}</strong></span>:<span><strong> {year}</strong></span>}
            </p>

            <div className='passoBox'>
                <img src={require(`./../../assets/image/User.png`)} alt="key" style={{ positon: 'absolute', marginTop: '22px', marginRight: '7px', width: '19px', height: '19px' }} />
                <TextField
                    id="standard-basic"
                    name='codigo'
                    value={codigo}
                    label="Cdigo do Estudante"
                    variant="standard"
                    color={comboErr ? 'error' : 'success'}
                    onChange={setFormB}
                    style={{ marginRight: '10px' }}
                />
                <span style={{ float: 'right', marginTop: '-145px', borderRadius: '5px', fontSize: '12px', padding: '3px', paddingRight: '10px', paddingLeft: '10px', backgroundColor: 'coral' }}>Ano de lanamento</span>
                <select name="ano" value={ano} onChange={setFormB} style={{ float: 'right', marginTop: '-118px' }}>
                    <option value={`${new Date().getFullYear()}`}>{`${new Date().getFullYear()}`}</option>
                    <option value={`${new Date().getFullYear() - 1}`}>{`${new Date().getFullYear() - 1}`}</option>
                    <option value={`${new Date().getFullYear() - 2}`}>{`${new Date().getFullYear() - 2}`}</option>
                    <option value={`${new Date().getFullYear() - 3}`}>{`${new Date().getFullYear() - 3}`}</option>
                    <option value={`${new Date().getFullYear() - 4}`}>{`${new Date().getFullYear() - 4}`}</option>
                    <option value={`${new Date().getFullYear() - 5}`}>{`${new Date().getFullYear() - 5}`}</option>
                </select> 
                <Button
                    variant='contained'
                    color="primary"
                    style={{ float: 'right', boxShadow: 'none', textTransform: 'none', marginTop: '-95px' }}
                    onClick={() => setYear(formDataB.ano)}
                >
                    Alterar ano de lanamento
                </Button>

                <Button
                    variant='contained'
                    color="primary"
                    style={{ marginTop: '10px', marginLeft: '0px', boxShadow: 'none', float: 'rigth' }}
                    onClick={getEstudante}
                >
                    Submeter
                </Button>

                <div id='show'>
                    <TableContainer component={Paper} style={{ marginTop: '15px' }}>
                        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell> <strong>Cdigo</strong> </TableCell>
                                    <TableCell> <strong>Estudante (Nome)</strong></TableCell>
                                    <TableCell> <strong>Gnero</strong></TableCell>
                                    <TableCell> <strong>rea (Curso)</strong></TableCell>
                                    <TableCell> <strong>Contacto (+258)</strong></TableCell>
                                    <TableCell>
                                        <Button variant="outlined" color="secondary" startIcon={<DeleteIcon />} onClick={() => setDataEstudante({})}>
                                            Remover
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                    <TableCell>{dataEstudante.codigoEstudante}</TableCell>
                                    <TableCell>{dataEstudante.firstName == undefined ? <span></span> : dataEstudante.firstName + " " + dataEstudante.lastName}</TableCell>
                                    <TableCell>{dataEstudante.genero}</TableCell>
                                    <TableCell>{dataEstudante.curso}</TableCell>
                                    <TableCell>{dataEstudante.telefone}</TableCell>
                                    <TableCell>
                                        {dataEstudante.firstName == undefined ? <span></span> : <Checkbox {...label} defaultChecked />}
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                        {wait ? <Skeleton style={{ height: '70px', marginTop: '-10px', marginLeft: 'auto', marginRight: 'auto', width: '98%' }} /> : <span></span>}
                        {comboErr ? <Alert severity="error">Este estudante no existe</Alert> : <span></span>}
                    </TableContainer>
                </div>


                <Box
                    sx={{
                        display: 'flex',
                        marginTop: '10px',
                        flexDirection: 'column',
                        alignItems: 'center',
                        '& > *': {
                            m: 1,
                        },
                    }}
                >
                    <ButtonGroup variant="outlined" aria-label="outlined button group">
                        <Button onClick={() => {
                            console.log(dataEstudante)
                            const isEmpty = Object.keys(dataEstudante).length === 0
                            console.log(isEmpty)
                            if (isEmpty) {
                                setComboErr(true)
                            } else {
                                navigation.next()
                            }
                            //
                        }}>Prximo</Button>
                    </ButtonGroup>
                </Box>
            </div>
        </div >
    )
}