import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import { Usuario } from '../Usuarios'
import Avatar from '@mui/material/Avatar'
import PersonIcon from '@mui/icons-material/Person'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemText from '@mui/material/ListItemText'
import { blue } from '@mui/material/colors'
import PersonAddAlt1OutlinedIcon from '@mui/icons-material/PersonAddAlt1Outlined';
import DeleteIcon from '@mui/icons-material/Delete'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import { Button, Dialog, DialogContent } from "@material-ui/core"
import axios from 'axios'
import { Grid } from "@material-ui/core"
import { DialogContentText, Skeleton } from '@mui/material'
import { useStep } from 'react-hooks-helper'
import { StepX } from './StepX'

const steps = [{ id: "base" }, { id: "stepX" }]

export const User = () => {
    const [process, setProcess] = useState(true)
    const [users, setUsers] = useState([])
    useEffect(() => {
        axios.get('api/user/').then(res => {
            console.log(res)

            if (res.data.data.data.length == 0) {
            } else {
                setUsers(res.data.data.data)
                setProcess(false)
            }
        }).catch(e => { console.log(e); setProcess(false) })
    }, [])

    const { step, navigation } = useStep({
        steps,
        initialStep: 0,
    })

    const props = { navigation }

    switch (step.id) {
        case "base":
            return (
                <div className="users">
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                        onClick={() => {
                            ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Usuario /></React.StrictMode>)
                        }}
                    >
                        <ArrowBackIosIcon />
                    </Button> <span>Usurios |</span>
                    <hr></hr>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                        onClick={() => navigation.next()}
                    >
                        <PersonAddAlt1OutlinedIcon />
                        <span style={{ fontSize: '13px', marginLeft: '10px' }}> Criar usurio</span>
                    </Button>

                    <Button
                        color="default"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '3px', marginLeft: '5px', marginTop: '0px', boxShadow: 'none' }}
                        onClick={() => ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><User /></React.StrictMode>)}
                    >
                        <span style={{ fontSize: '13px' }}> Actualizar lista</span>
                    </Button>

                    <hr></hr>
                    {process ?
                        <Grid>
                            <Skeleton style={{ height: '60px', marginTop: '0px' }} />
                            <Skeleton style={{ height: '200px', marginTop: '-53px' }} />
                            <Skeleton style={{ height: '60px', marginTop: '-48px' }} />
                        </Grid>
                        :
                        <List style={{ boxShadow: 'rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px' }}>
                            {users.map(el => (<ListItem key={el._id}>
                                <Avatar sx={{ bgcolor: blue[100], color: blue[600] }}>
                                    <PersonIcon />
                                </Avatar>
                                <ListItemText
                                    style={{ marginLeft: '10px' }}
                                    primary={el.username}
                                    secondary={el.email + " | " + el.role}
                                />

                                <Button
                                    color="default"
                                    variant="contained"
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', marginLeft: '5px', boxShadow: 'none' }}
                                    onClick={() => {
                                        alert('Deseja eliminar o usurio: ' + el.email +'?')
                                        if (el.role === 'admin' || el.role === 'user') {
                                            axios.delete(`/api/user/${el._id}`).then(res => {
                                                console.log(res)
                                                ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><User /></React.StrictMode>)
                                            })
                                        } else {
                                            if (el.role === 'formador') {
                                                axios.delete(`/api/formador/${el.email}`).then(res => {
                                                    console.log(res)
                                                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><User /></React.StrictMode>)
                                                })
                                            }
                                        }

                                    }}
                                >
                                    <DeleteIcon fontSize='medium' /><span style={{ fontSize: '12px' }}>Eliminar</span>
                                </Button>
                            </ListItem>))}
                        </List>}

                </div>
            )

        case "stepX":
            return (
                <div className="users">
                    <StepX {...props} />
                </div>
            )

    }
}