import React, { useEffect, useState } from 'react'
import Header from './../pages/Header'
import ReactDOM from 'react-dom/client'
import { Usuario } from './Usuarios'
import { Button } from '@mui/material'
import { Link } from 'react-router-dom'
import './../assets/css/allPages.css'
import { Principal } from './Principal'


const Dashboard = ({ user, isLoggedIn }) => {

    return (
        <div className='home'>
            <Header auth={isLoggedIn} user={user} />
            <div>
                <Button
                    color="primary"
                    variant="contained"
                    style={{ borderRadius: '0px 5px 5px 0px', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                    onClick={() => {
                        //ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Principal /></React.StrictMode>)
                        window.location.href = '/'
                    }}
                > Principal
                </Button>

                <Link to='/repositorio'>
                <Button
                    color="primary"
                    variant="contained"
                    style={{ marginTop: '10px', marginRight: '10px', textTransform: 'none', boxShadow: 'none' }}
                > Repositrio
                </Button>
                </Link>
                

                <Button
                    color="primary"
                    variant="contained"
                    style={{ marginTop: '10px', marginRight: '10px', textTransform: 'none', boxShadow: 'none' }}
                    onClick={(e) => {
                        ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><Usuario /></React.StrictMode>)
                    }}
                > Gestor de usurios
                </Button>
            </div>
            <hr></hr>
            <div id='desktop'>
                <Principal />
            </div>
        </div>
    )
}

export default Dashboard