import React, { useEffect, useState } from 'react'
import Button from '@mui/material/Button'
import ButtonGroup from '@mui/material/ButtonGroup'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Avatar from '@mui/material/Avatar'
import FolderIcon from '@mui/icons-material/Folder'
import { grey } from '@mui/material/colors'
import InputAdornment from '@mui/material/InputAdornment'
import MenuItem from '@mui/material/MenuItem'
import { Input } from '@mui/material'
import './../../assets/css/stepCss.css'

const materia = [
    { material: 'Avaliao' },
    { material: 'Reavaliao' }
]

const materiaNum = [
    { material: '1' },
    { material: '2' }
]

const materiaTipo = [
    { classe: 'Evidncia de Conhecimento' },
    { classe: 'Evidncia de Desempenho' }
]



export const PassoC = ({ dataModulo, setDataModulo, pdf, setPdf, formData, setForm, navigation }) => {
    //const { firstName, lastName, dateBirth, genero, local_nascimento } = formData
    const [pdfFile, setPdfFile] = useState(null);
    const [value, setValue] = React.useState(new Date())
    const [pdfFileError, setPdfFileError] = useState('')
    const [apag, setApag] = useState(true)

    const { go } = navigation

    const handleChange = (newValue) => {
        setValue(newValue)
        setForm(data_de_realizacao)
    }





    const {
        tipo_de_evidencia,
        resultado_de_aprendizagem,
        classificacao,
        nota,
        numero_do_material,
        material,
        data_de_realizacao,
        data_de_lancamento,
        ano,
        tutela
    } = formData

    useEffect(() => {
        console.log(dataModulo);
        if (dataModulo.elementos_de_competencia == '') {
            console.log(dataModulo.elementos_de_competencia + ' Vazio')
        }

    
    }, [])

    var currencies = dataModulo.elementos_de_competencia
    console.log(currencies);

    

    const fileSizeFormater = (bytes, decimal) => {
        if (bytes === 0) {
            return '0 Bytes'
        }
        const dm = decimal || 2
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
        const index = Math.floor(Math.log(bytes) / Math.log(1000))
        return parseFloat((bytes / Math.pow(1000, index)).toFixed(dm)) + ' ' + sizes[index]
    }

    const fileType = ['application/pdf']
    const handlePdfFileChange = (e) => {
        let selectedFile = e.target.files[0]
        console.log(selectedFile)
        if (selectedFile) {
            if (selectedFile && fileType.includes(selectedFile.type)) {
                setPdfFileError('')
                let reader = new FileReader()
                reader.readAsArrayBuffer(selectedFile)
                reader.onloadend = (e) => {
                    const pdfAtributes = {
                        Nome: selectedFile.name,
                        Tamanho: fileSizeFormater(selectedFile.size, 2),
                        Tipo: selectedFile.type,
                        Buffer_PDF: e.target.result
                    }
                    setPdfFile(pdfAtributes);
                    setPdf(pdfAtributes)

                    console.log(pdfAtributes)
                }
            } else {
                setPdfFileError('Por favor seleccione um arquivo PDF vlido')
            }
        }else{
            console.log('no file');
            
        }
    }

    return (
        <div className="passoA" style={{ marginBottom: '100px' }}>
            <div style={{ marginTop: '20px', marginLeft: '10px' }}>
                <Avatar sx={{ bgcolor: grey[500] }}>
                    <FolderIcon />
                </Avatar>
                Arquivo</div>

            <div className='evidenc-fields'>
                <TextField
                    id="outlined-select-currency"
                    select
                    label="RA  N"
                    name="resultado_de_aprendizagem"
                    value={resultado_de_aprendizagem}
                    onChange={setForm}
                    helperText="Seleccione o resultado de aprendizagem"
                    size="small"
                    style={{ margin: "5px", marginBottom: '20px', width: '97%' }}
                >
                    {currencies.map((option) => (
                        <MenuItem key={option.RA_numero} value={option.RA_numero + " - " + option.resultado_de_aprendizagem}>
                            {option.RA_numero}<span> - </span>{option.resultado_de_aprendizagem}
                        </MenuItem>
                    ))}
                </TextField>

                <TextField
                    id="outlined-select-currency"
                    select
                    label="Evidncia material"
                    name="material"
                    value={material}
                    onChange={setForm}
                    helperText="Seleccione a Evidncia material"
                    size="small"
                    style={{ margin: "5px", marginBottom: '20px', width: '97%' }}
                >
                    {materia.map((option) => (
                        <MenuItem key={option.material} value={option.material}>
                            {option.material}
                        </MenuItem>
                    ))}
                </TextField>

                    <TextField
                        label="N RAV"
                        id="outlined-select-currency"
                        select
                        className='tip'
                        name="numero_do_material"
                        color={ material === 'Avaliao' && numero_do_material == '2' ? 'error' : 'primary' }
                        value={numero_do_material}
                        disabled={material === '' ||  material === 'Reavaliao' ? false : numero_do_material == '2' ? false : true }
                        onChange={setForm}
                        helperText={ material=== 'Avaliao' && numero_do_material == '2' ? <span style={{fontSize: '11px', color: 'red'}}>Por favor, seleccione o numero (1) para este material de evidencia </span> : material === 'Reavaliao' ? "Seleccione o numero da evidncia material" : ""}
                        size="small"
                        style={{ margin: "5px", width: '16ch', marginBottom: '20px' }}
                        
                    >
                        {materiaNum.map((option) => (
                            <MenuItem key={option.material} value={option.material}>
                                {option.material}
                            </MenuItem>
                        ))}
                    </TextField>

                <TextField
                    label="NOTA"
                    id="outlined-start-adornment"
                    name="nota"
                    value={nota}
                    color={nota < 80 ? "error" : "success"}
                    onChange={setForm}
                    sx={{ m: 1, width: '16ch', margin: "5px", marginBottom: '20px' }}
                    InputProps={{
                        endAdornment: <InputAdornment position="end">%</InputAdornment>
                    }}
                />
                <TextField
                    id="outlined-select-currency"
                    select
                    className='tip'
                    label="Tipo de Evidncia"
                    name="tipo_de_evidencia"
                    value={tipo_de_evidencia}
                    onChange={setForm}
                    helperText="Seleccione o tipo de Evidncia"
                    size="small"
                    style={{ margin: "5px", marginBottom: '20px' }}
                >
                    {materiaTipo.map((option) => (
                        <MenuItem key={option.classe} value={option.classe}>
                            {option.classe}
                        </MenuItem>
                    ))}
                </TextField>

                <form style={{ border: '1px solid grey', width: '150px', borderRadius: '3px', padding: '5px', marginTop: '5px', marginLeft: '5px' }}>
                    <Input
                        type='Date'
                        name="data_de_realizacao"
                        value={data_de_realizacao}
                        onChange={setForm}
                        sx={{ m: 1, width: '16ch' }}
                    /><br></br>
                    <span style={{ marginLeft: '2px' }}>Data de realizao</span>
                </form>

                <input type="file" className='form-control'
                    required onChange={handlePdfFileChange}
                    style={{ marginTop: '30px', color: 'green' }}
                />
                {pdfFileError && <div className='error-msg'>{pdfFileError}</div>}
                <em><span style={{fontSize: '12px', color: 'red', float: 'right'}}>Tamanho limite do ficheiro PDF* (10Mb)</span></em>
            </div>
            <Box
                sx={{
                    display: 'flex',
                    marginTop: '10px',
                    flexDirection: 'column',
                    alignItems: 'center',
                    '& > *': {
                        m: 1,
                    },
                }}
            >
                <ButtonGroup variant="outlined" aria-label="outlined button group">
                    <Button color="secondary" onClick={() => {
                        window.location.href = '/registopedagogico'
                    }}>Cancelar</Button>
                    <Button onClick={() => navigation.next()}>Submeter</Button>
                </ButtonGroup>
            </Box>
        </div>
    )
}