import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import React, { useState, useEffect } from "react"
import { useForm } from "react-hooks-helper"
import Button from '@material-ui/core/Button'
import Box from '@mui/material/Box'
import LinearProgress from '@mui/material/LinearProgress'
import Alert from '@mui/material/Alert'
import Stack from '@mui/material/Stack'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import axios from 'axios'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import Select from '@mui/material/Select'
import { Autocomplete } from '@mui/material'


const defaultData = {
    nome: "",
    bi: "",
    email: "",
    username: "",
    telefone: "",
    senha: "",
    confirmSenha: ""
}

export const StepX = ({ navigation }) => {
    const [formData, setForm] = useForm(defaultData)
    const [isShow, setShow] = useState(false)
    const [helpA, setHelpA] = useState(false)
    const [helpB, setHelpB] = useState(false)
    const [helpC, setHelpC] = useState(false)
    const [helpD, setHelpD] = useState(false)
    const [helpE, setHelpE] = useState(false)
    const [helpF, setHelpF] = useState(false)
    const [not, setNot] = useState(false)
    const [er, setEr] = useState(false)
    const [suc, setSuc] = useState(false)

    const {
        nome,
        bi,
        email,
        role,
        username,
        telefone,
        senha,
        confirmSenha
    } = formData

    const registar = () => {

     
        setEr(false)
        setShow(true)

        const user = {
            nome_completo: formData.nome,
            bi: formData.bi,
            email: formData.email,
            contacto: formData.telefone,
            data: new Date().toLocaleString()
        }

        const sign = {
            username: formData.nome,
            email: formData.email,
            role: formData.role,
            password: formData.senha,
            passwordConfirm: formData.confirmSenha,
        }

        if (formData.nome === '') {
            setHelpA(true)
        } else {
            setHelpA(false)
        }

        if (formData.bi === '') {
            setHelpB(true)
        } else {
            setHelpB(false)
        }

        if (formData.email === '') {
            setHelpC(true)
        } else {
            setHelpC(false)
        }

        if (formData.telefone === '') {
            setHelpD(true)
        } else {
            setHelpD(false)
        }

        if (formData.senha === '') {
            setHelpE(true)
        } else {
            setHelpE(false)
        }

        if (formData.confirmSenha === '') {
            setHelpF(true)
        } else {
            setHelpF(false)
        }

        if (formData.senha === formData.confirmSenha) {
            setNot(false)
        } else {
            setNot(true)
        }

        axios.post("/api/user/create", sign)
            .then(result => {
               
                if(formData.role === 'formador'){
                    axios.post('/api/formador/register', user)
                }
                setSuc(true)

                setTimeout(() => {
                    setSuc(false)
                }, 1000);
                setShow(false)
            })
            .catch(error => {
                setSuc(false)
                setShow(false)
                setEr(true)
                var e = {error}
                console.log(e.error);
                alert(e.error +" Possivel error:: Verifique se este usurio j esta registado!")
                
            })
    }

    return (
        <div className="users">

            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                onClick={() => {
                    navigation.previous()
                }}
            >
                <ArrowBackIosIcon />
            </Button> <span>Criar usurio |</span>

            <p style={{ fontSize: '10px', fontFamily: 'Verdana !important', textAlign: 'center', paddingBottom: '10px', boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px' }} >
                <strong>REGISTAR</strong></p>
                {isShow ? <Box style={{marginTop: '-12px', marginBottom: '10px'}}>
                <LinearProgress />
            </Box>
                : <span></span>
            }
            <div className="signin">
                <form style={{ marginBottom: '50px', marginLeft: 'auto', marginRight: 'auto', width: '100%', padding: '5px' }}>
                    <TextField
                        id="outlined"
                        label="Nome completo do formador"
                        name="nome"
                        value={nome}
                        onChange={setForm}
                        helperText={helpA ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, preencha este compo com o seu nome!</span> : ''}
                        color={helpA ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        label="BI"
                        name="bi"
                        value={bi}
                        onChange={setForm}
                        helperText={helpB ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, preencha este compo com o seu numero de BI!</span> : ''}
                        color={helpB ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type={"email"}
                        label="Email"
                        name="email"
                        value={email}
                        onChange={setForm}
                        helperText={helpC ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos o seu email!</span> : ''}
                        color={helpC ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type="tel"
                        label="Contacto"
                        name="telefone"
                        value={telefone}
                        onChange={setForm}
                        helperText={helpD ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos o seu contacto!</span> : ''}
                        color={helpD ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>(+258)</InputAdornment>
                        }}
                    />
                    <p style={{ fontSize: '12px', color: 'red' }}> */ A senha fornecida deve conter 8 caracters no min /*</p>
                    <TextField
                        id="outlined"
                        type={"password"}
                        label="Senha de acesso"
                        name="senha"
                        value={senha}
                        onChange={setForm}
                        helperText={helpE ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos uma senha!</span> : ''}
                        color={helpE ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type={"password"}
                        label="Confirmar senha de acesso"
                        name="confirmSenha"
                        value={confirmSenha}
                        onChange={setForm}
                        helperText={helpF ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, confirme a senha fornecida!</span> : ''}
                        color={helpF ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <Autocomplete
                        options={['admin', 'formador', 'user']}
                        size="small"
                        style={{width: '200px'}}
                        renderInput={(params) =>
                            <TextField {...params} name="role" value={role} onSelect={setForm} label="Usurio" variant="outlined" />}
                    />

                    {suc ? <Stack sx={{ width: '100%', marginTop: '10px' }} spacing={2}>
                        <Alert severity="success">Criado com sucesso com sucesso!</Alert>
                    </Stack> : <span></span>}

                    {er? <Stack sx={{ width: '100%', marginTop: '10px' }} spacing={2}>
                        <Alert severity="error">Registo invalido!</Alert>
                    </Stack> : <span></span>}
                    {not ? <Alert severity="error">Os passwords fornecidos no so iguais</Alert>
                        : <span></span>
                    }

                    <br></br>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ borderRadius: '', boxShadow: 'none', height: '37px', marginBottom: '20px', marginTop: '0px', textTransform: 'none', textDecoration: 'none' }}
                        onClick={registar}
                    >
                        Submeter registo</Button>

             <hr></hr>
                </form>
            </div>
        </div>
    )
}

