import React, { useState, useEffect } from "react";
import './../assets/css/login.css'
import Button from '@material-ui/core/Button'
import { CircularProgress, Input, TextField } from '@material-ui/core';
import {useNavigate } from 'react-router-dom'
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogContent from '@mui/material/DialogContent'
import DialogContentText from '@mui/material/DialogContentText'
import DialogTitle from '@mui/material/DialogTitle'
import Slide from '@mui/material/Slide'
import { useForm } from "react-hooks-helper"
import axios from 'axios'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />
})

const imgSty = {
    position: 'relative',
    top: '-1px',
}

var myHeaders = new Headers();
myHeaders.append("Content-Type", "application/json");
myHeaders.append("Cookie", "");

const defaultData = {
    remail: "",
    email: "",
    password: "",
    rsenha: "",
    message: ""
}

function Login() {
    const [load, setLoad] = useState(true)
    const [open, setOpen] = useState(false)

    const [isShow, setShow] = useState(false);

    const [err, setErr] = useState(false);
    const [suc, setSuc] = useState(false);
    const [sucer, setSucer] = useState(false);
    const [isError, setError] = useState(false);
    const navigate = useNavigate()

    const [formData, setForm] = useForm(defaultData)

    const [logData, setLog] = useForm({
        username: "",
        passw: ""
    })

    const {
        remail,
        email,
        password,
        rsenha,
        message
    } = formData

    const {
        username,
        passw
    } = logData

    useEffect(() => {
        setTimeout(() => {
            setLoad(false)
        }, 4000);
    }, [])

    const Log = () => {
        setShow(true)
        var raw = JSON.stringify({ "email": logData.username, "password": logData.passw });
        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        }

        fetch("/api/user/login", requestOptions)
            .then(response => response.json())
            .then(result => {
                console.log(result)
                if (result.status === 'success') {
                    setShow(true)
                    setError(false)
                    setTimeout(() => {
                        window.location.href = "/"
                    }, 1000)
                }
            })
            .catch(error => {
                console.log('error', error)
                setError(true)
                setTimeout(() => {
                    setShow(false)
                }, 300);
            });
    }

    const handleKeyDown = (e) => {
        if (e.key === 'Enter') {
         Log()
        }
    }

    const handleClickOpen = () => {
        setErr(false)
        setSuc(false)
        setSucer(false)
        setOpen(true)
    }

    const handleClose = () => {
        setOpen(false)
    }


    const send = () => {
        const sms = {
            email: formData.remail,
            newPassword: formData.rsenha,
            message: formData.message
        }
        //console.log(sms);
        axios.post(`/api/user/forgotPassword`, sms)
            .then(res => {
                if (res.data.status === 'success') {
                    setErr(false)
                    console.log('go')
                    setSuc(true)
                    setTimeout(() => {
                        setSucer(true)
                        setSuc(false)
                    }, 2000)

                    setTimeout(() => {
                        setOpen(false)
                    }, 3000)
                }
            }).catch(e => {
                console.log(e);
                setErr(true)
            })
    }

    return (
        <div className='login'>
            {isShow ? <Box sx={{ width: '100%', position: 'relative', top: '-50px' }}>
                <LinearProgress />
            </Box>
                : <span></span>
            }
            <img src={require(`./../assets/image/vex.png`)} alt="key" style={{ width: '64px', heigth: 'auto', margin: 'auto', display: 'block' }} />
            <em><p style={{ fontSize: '10px', fontFamily: 'Verdana', textAlign: 'center', paddingBottom: '10px', boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px' }} >
                <strong>Bem - Vindo(a) ao Sistema DOM</strong>  </p></em>
            <form action="">
                <header>
                    <p style={{ fontSize: '16px', fontFamily: 'Verdana', textAlign: 'center', color: 'white', boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px' }} >
                        <strong>USURIO</strong>  </p>
                </header>
                <p> <img src={require(`./../assets/image/User.png`)} alt="key" style={{ positon: 'relative', top: '2px', width: '19px', height: '19px' }} /> Username</p>
                <Input
                    type='text'
                    name="username"
                    value={username}
                    onChange={setLog}
                    fullWidth
                    placeholder=""
                />
                <br />

                <p> <img src={require(`./../assets/image/pass.png`)} alt="key" style={imgSty} /> Senha</p>
                <Input
                    type='password'
                    name="passw"
                    value={passw}
                    onChange={setLog}
                    onKeyDown={handleKeyDown}
                    fullWidth
                    placeholder="..."
                />
                <br />
                <Button
                    color="primary"
                    variant="contained"
                    onClick={Log}
                    style={{ marginTop: '10px', boxShadow: 'none', textTransform: 'none' }}
                > Entrar</Button>

            </form>

            {isError ? <Stack sx={{ width: '100%', marginTop: '10px' }} spacing={2}>
                <Alert severity="error">Usuario/senha errada(o)</Alert>
            </Stack>
                :
                <p></p>
            }

            <div>
                <Dialog
                    open={open}
                    TransitionComponent={Transition}
                    keepMounted
                    onClose={handleClose}
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle style={{ fontSize: '14px' }}>
                        {"Centro de apoio ao usurio (Formador*)"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-slide-description">
                            Caso tenha esquecido o seu password/senha, recupere este credencial
                            digitando o seu USER [email] e uma [nova senha] noS campos abaixo.
                        </DialogContentText>
                        <DialogContent>
                            <TextField
                                id="outlined"
                                label="Email"
                                name="remail"
                                value={remail}
                                onChange={setForm}
                                helperText="Por favor, preencha este compo com o seu email de acesso!"
                                size="small"
                                style={{ marginBottom: '10px', width: '100%' }}
                            />

                            <TextField
                                id="outlined"
                                label="Nova senha"
                                name="rsenha"
                                value={rsenha}
                                onChange={setForm}
                                helperText="Esta senha deve conter no minimo 8 caracteres!"
                                size="small"
                                style={{ marginBottom: '20px', width: '100%' }}
                            />

                            <TextField
                                id="contained"
                                label="Mensagem"
                                name="message"
                                value={message}
                                onChange={setForm}
                                helperText="Por favor, mande um feedback sobre este processo!"
                                style={{ marginBottom: '0px', width: '100%' }}
                            />

                            {err ? <Alert color="error">Requisio no enviada (err: email invalido)</Alert> : ""}
                        </DialogContent>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            color="primary"
                            variant="outlined"
                            style={sucer ? { textTransform: 'none', color: 'green', border: 'none' } : { textTransform: 'none' }}
                            onClick={send}
                        >
                            {sucer ? "Pediddo enviado com sucesso!" : suc ? <CircularProgress size={22} /> : "Enviar"}
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>

            <p style={{ textAlign: 'center', marginTop: '60px' }}>

                <Button
                    color="primary"
                    variant="outlined"
                    style={{ borderRadius: '5px', marginTop: '0px', textTransform: 'none', textDecoration: 'none' }}
                    onClick={() => {
                        window.location.href = "/signup"
                    }}
                >
                    Registar-se no DOMS</Button> <br></br>
                <Button
                    color="default"
                    variant="outlined"
                    onClick={handleClickOpen}
                    style={{ borderRadius: '5px', marginTop: '5px', boxShadow: 'none', marginLeft: '0px', textTransform: 'none' }}
                > Centro de apoio#</Button>
            </p>
        </div>


    );
}


export default Login;