import React, { useState, useEffect } from "react"
import { useForm } from "react-hooks-helper"
import './../assets/css/login.css'
import Button from '@material-ui/core/Button'
import Box from '@mui/material/Box'
import LinearProgress from '@mui/material/LinearProgress'
import Alert from '@mui/material/Alert'
import Stack from '@mui/material/Stack'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import axios from 'axios'


const defaultData = {
    nome: "",
    bi: "",
    email: "",
    username: "",
    telefone: "",
    senha: "",
    confirmSenha: ""
}

const Signup = () => {
    const [formData, setForm] = useForm(defaultData)
    const [isShow, setShow] = useState(false)
    const [helpA, setHelpA] = useState(false)
    const [helpB, setHelpB] = useState(false)
    const [helpC, setHelpC] = useState(false)
    const [helpD, setHelpD] = useState(false)
    const [helpE, setHelpE] = useState(false)
    const [helpF, setHelpF] = useState(false)
    const [not, setNot] = useState(false)
    const [er, setEr] = useState(false)
    const [suc, setSuc] = useState(false)

    const {
        nome,
        bi,
        email,
        username,
        telefone,
        senha,
        confirmSenha
    } = formData

    const registar = () => {

        const formador = {
            nome_completo: formData.nome,
            bi: formData.bi,
            email: formData.email,
            contacto: formData.telefone,
            data: new Date().toLocaleString()
        }

        const sign = {
            username: formData.nome,
            email: formData.email,
            password: formData.senha,
            passwordConfirm: formData.confirmSenha,
        }

        if (formData.nome === '') {
            setHelpA(true)
        } else {
            setHelpA(false)
        }

        if (formData.bi === '') {
            setHelpB(true)
        } else {
            setHelpB(false)
        }

        if (formData.email === '') {
            setHelpC(true)
        } else {
            setHelpC(false)
        }

        if (formData.telefone === '') {
            setHelpD(true)
        } else {
            setHelpD(false)
        }

        if (formData.senha === '') {
            setHelpE(true)
        } else {
            setHelpE(false)
        }

        if (formData.confirmSenha === '') {
            setHelpF(true)
        } else {
            setHelpF(false)
        }

        if (formData.senha === formData.confirmSenha) {
            setNot(false)
        } else {
            setNot(true)
        }

        axios.post("/api/user/signup", sign)
            .then(result => {
                setSuc(true)
                setEr(false)
                setShow(true)
                setTimeout(() => {
                    window.location.href = "/login"
                }, 1000)

                axios.post('/api/formador/register', formador)
            })
            .catch(error => {
                setSuc(false)
                setShow(false)
                setEr(true)
            })
    }

    return (
        <div className='x'>
            {isShow ? <Box sx={{ width: '100%', marginLeft: '0px !mportant', marginRight: 'auto', position: 'fixed', top: '0px' }}>
                <LinearProgress />
            </Box>
                : <span></span>
            }
            <img src={require(`./../assets/image/vex.png`)} alt="key" style={{ width: '64px', heigth: 'auto', margin: 'auto', display: 'block' }} />
            <p style={{ fontSize: '10px', fontFamily: 'Verdana !important', textAlign: 'center', paddingBottom: '10px', boxShadow: 'rgba(17, 17, 26, 0.1) 0px 1px 0px' }} >
                <strong>REGISTAR</strong></p>
            <div className="signin">
                <form style={{ marginBottom: '50px', marginLeft: 'auto', marginRight: 'auto', width: '100%', padding: '5px' }}>
                    <TextField
                        id="outlined"
                        label="Nome completo do formador"
                        name="nome"
                        value={nome}
                        onChange={setForm}
                        helperText={helpA ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, preencha este compo com o seu nome!</span> : ''}
                        color={helpA ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        label="BI"
                        name="bi"
                        value={bi}
                        onChange={setForm}
                        helperText={helpB ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, preencha este compo com o seu numero de BI!</span> : ''}
                        color={helpB ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type={"email"}
                        label="Email"
                        name="email"
                        value={email}
                        onChange={setForm}
                        helperText={helpC ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos o seu email!</span> : ''}
                        color={helpC ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type="tel"
                        label="Contacto"
                        name="telefone"
                        value={telefone}
                        onChange={setForm}
                        helperText={helpD ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos o seu contacto!</span> : ''}
                        color={helpD ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}>(+258)</InputAdornment>
                        }}
                    />
                    <p style={{ fontSize: '10px', color: 'red' }}> */ A senha fornecida deve conter 8 caracters no min /*</p>
                    <TextField
                        id="outlined"
                        type={"password"}
                        label="Senha de acesso"
                        name="senha"
                        value={senha}
                        onChange={setForm}
                        helperText={helpE ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, fornea-nos uma senha!</span> : ''}
                        color={helpE ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    <TextField
                        id="outlined"
                        type={"password"}
                        label="Confirmar senha de acesso"
                        name="confirmSenha"
                        value={confirmSenha}
                        onChange={setForm}
                        helperText={helpF ? <span style={{ color: 'red', fontSize: '11px' }}>Por favor, confirme a senha fornecida!</span> : ''}
                        color={helpF ? 'error' : 'primary'}
                        size="small"
                        style={{ marginRight: '5px', marginBottom: '10px', width: '250px' }}
                    />

                    {suc ? <Stack sx={{ width: '100%', marginTop: '10px' }} spacing={2}>
                        <Alert severity="success">Pedido submetido com sucesso!</Alert>
                    </Stack> : <span></span>}

                    {er ? <Stack sx={{ width: '100%', marginTop: '10px' }} spacing={2}>
                        <Alert severity="error">Registo invalido!</Alert>
                    </Stack> : <span></span>}
                    {not ? <Alert severity="error">Os passwords fornecidos no so iguais</Alert>
                        : <span></span>
                    }

                    <Button
                        color="primary"
                        variant="contained"
                        style={{ borderRadius: '', boxShadow: 'none', height: '39px', marginBottom: '20px', marginTop: '0px', textTransform: 'none', textDecoration: 'none' }}
                        onClick={registar}
                    >
                        Submeter registo</Button>

                    <p style={{ marginBottom: '15px', fontSize: '12px', width: '70%', marginTop: '40px', padding: '0px', textAlign: 'justify' }}><hr></hr>
                        Caro formador, aps submeter este registo, dever aguardar a confirmao das suas credncias num prazo de um dia til.<br></br><hr></hr>
                        <span style={{ color: 'green' }}>NB</span> : S poder ter acesso a plataforma depois da confirmao das suas credenciais.
                    </p>

                </form>
            </div>
        </div>
    )
}
export default Signup