import img from './../assets/image/files.jpg'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import CardMedia from '@mui/material/CardMedia'
import { Button } from '@mui/material'
import { useForm, useStep } from 'react-hooks-helper'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import Header from '../pages/Header'

const Menu = ({ user, isLoggedIn }) => {
    useEffect(() => {
      console.log(user)
    }, [])
    
    return (
        <div className='home'>
            <Header auth={isLoggedIn} user={user} />
            <div className="users">
            <Button
                    color="primary"
                    variant="contained"
                    style={{ borderRadius: '0px 5px 5px 0px', marginTop: '10px', marginRight: '10px', textTransform: 'none' }}
                    onClick={() => window.location.href = '/'}
                >Principal
                </Button>
                <hr></hr>
                    <h4>Repositrio de Arquivos</h4>
                    <hr></hr>
                    <div className="division">
                        {user.role === 'admin' ? <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={img}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Arquivo de Evidncia
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                >
                                    <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>: ''}

                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={img}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Estudantes 
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                >
                                    <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>

                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={img}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Unidades de Competncia
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                >
                                   <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>

                        <div>
                            <Card sx={{ maxWidth: 345 }} style={{ padding: '5px' }}>
                                <CardMedia
                                    component="img"
                                    height="120"
                                    image={img}
                                    alt="mec"
                                />
                                <CardContent>
                                    <span style={{ color: 'silver' }}></span> Arquivo de Inscries
                                </CardContent>
                                <Button
                                    color="primary"
                                    variant="contained"
                                    fullWidth
                                    style={{ textTransform: 'none', borderRadius: '3px', marginTop: '0px', boxShadow: 'none' }}
                                >
                                   <ArrowForwardIcon/>
                                </Button>
                            </Card>
                        </div>

                    </div>
                </div>
        </div>
    )
}

export default Menu